export const rad = function (x: number): number {
  return (x * Math.PI) / 180;
};

interface IPlaceCoord {
  lat: number;
  lng: number;
}

export const getDistance = function (p1: IPlaceCoord, p2: IPlaceCoord): number {
  let R = 6378137; // Earth’s mean radius in meter
  let dLat = rad(p2.lat - p1.lat);
  let dLong = rad(p2.lng - p1.lng);
  let a =
    Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos(rad(p1.lat)) *
      Math.cos(rad(p2.lat)) *
      Math.sin(dLong / 2) *
      Math.sin(dLong / 2);
  let c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  let d = R * c; // distance in meter
  let miles = d / 1609.344;
  return Number(miles.toFixed(1)); // returns the distance in miles
};

export const arrayContainsArray = (superset, subset): boolean => {
  if (0 === subset.length) {
    return false;
  }
  return subset.every(function (value) {
    return superset.indexOf(value) >= 0;
  });
};
