const IS_DATA_LOADING = 'IS_DATA_LOADING';

interface IDataLoadingAction {
  type: string;
  payload: boolean;
}

export const setDataLoading = (cond: boolean): IDataLoadingAction => ({
  type: IS_DATA_LOADING,
  payload: cond
});

export default (state: boolean = false, action: IDataLoadingAction) => {
  switch (action.type) {
    case IS_DATA_LOADING:
      return action.payload;
    default:
      return state
  }
}