import { combineReducers } from 'redux';
import tires, { ITireDenormalized } from './tires';
import shops, { IShop } from './shops';
import sortBy from './sortBy';
import filteredTires from './filteredTires';
import selectedShop from './selectedShop';
import selectedTire from './selectedTire';
import center, { ICenter } from './center';
import shopsIndexed, { IShopsIndexed } from './shopsIndexed';
import tiresByShopId, { ITiresByShopId } from './tiresByShopId';
import filtersType, { IFiltersType } from './filtersType';
import tiresByType, { ITiresByType } from './tiresByType';
import tiresIndexed, { ITiresIndexed } from './tiresIndexed';
import filtersSize, { IFiltersSize } from './filtersSize';
import filtersBrands, { IFiltersBrands } from './filtersBrands';
import tiresByBrand, { ITiresByBrand } from './tiresByBrand';
import isDataLoading from './isDataLoading';
import tiresHomePage from './tiresHomePage';
import selectedDropdown from './selectedDropdown';
import selectedLayout, { EnumLayouts } from './selectedLayout';
import openSearchForm from './openSearchForm';
import isModalOpen from './isModalOpen';
import inShopServiceSelected from './inShopServiceSelected';
import roadServiceSelected from './roadServiceSelected';
import open247Selected from './open247Selected';
import open247MobileSelected from './open247MobileSelected';
import filteredShopsIDS from './filteredShopsIDS';
import tiresBySize from './tiresBySize';

export interface IState {
  center: ICenter;
  shops: IShop[];
  tires: ITireDenormalized[];
  tiresIndexed: ITiresIndexed;
  tiresByShopId: ITiresByShopId;
  tiresByType: ITiresByType;
  shopsIndexed: IShopsIndexed;
  filteredTires: ITireDenormalized[];
  sortBy: string;
  selectedShop: IShop[];
  selectedTire: ITireDenormalized[];
  filtersType: IFiltersType;
  filtersSize: any;
  filtersBrands: any;
  tiresByBrand: ITiresByBrand;
  isDataLoading: boolean;
  tiresHomePage: ITireDenormalized[];
  selectedDropdown: string;
  selectedLayout: EnumLayouts;
  openSearchForm: boolean;
  isModalOpen: boolean;
  inShopServiceSelected: boolean;
  roadServiceSelected: boolean;
  open247Selected: boolean;
  open247MobileSelected: boolean;
  filteredShopsIDS: string[];
}

// export default combineReducers<IState>({
const appReducer = combineReducers<IState>({
  center,
  tires,
  tiresIndexed,
  tiresByShopId,
  tiresByType,
  shops,
  shopsIndexed,
  filteredTires,
  sortBy,
  selectedShop,
  selectedTire,
  filtersType,
  filtersSize,
  filtersBrands,
  tiresByBrand,
  isDataLoading,
  tiresHomePage,
  selectedDropdown,
  selectedLayout,
  openSearchForm,
  isModalOpen,
  inShopServiceSelected,
  roadServiceSelected,
  open247Selected,
  open247MobileSelected,
  filteredShopsIDS,
  tiresBySize
});

const rootReducer = (state, action) => {
  // when a logout action is dispatched it will reset redux state
  if (action.type === 'RESET_STORE') {
    state = undefined;
  }

  return appReducer(state, action);
};

export default rootReducer;
