const SET_TIRES_BY_TYPE = "SET_TIRES_BY_TYPE";

export interface ITiresByType {
  All: number[];
  Trailer: number[];
  Drive: number[];
  Steer: number[];
}

interface ITiresByTypeAction {
  type: string;
  payload: ITiresByType;
}

export const setTiresByType = (tiresType: ITiresByType): ITiresByTypeAction => ({
  type: SET_TIRES_BY_TYPE,
  payload: tiresType
})

const initialState = {
  All: [],
  Trailer: [],
  Drive: [],
  Steer: []
}

export default (state: ITiresByType = initialState, action: ITiresByTypeAction) => {
  switch (action.type) {
    case SET_TIRES_BY_TYPE:
      return action.payload;
    default:
      return state;
  }
};