const SET_FILTERED_SHOPS_IDS = "SET_FILTERED_SHOPS_IDS";

interface IShopsAction {
  type: string;
  payload: string[];
}

export const setFilteredShopsIDS = (shopsIDS: string[]): IShopsAction => ({
  type: SET_FILTERED_SHOPS_IDS,
  payload: shopsIDS
});

export default (state: string[] = [], action: IShopsAction): string[] => {
  switch (action.type) {
    case SET_FILTERED_SHOPS_IDS:
      return action.payload;
    default:
      return state;
  }
}