import { IState } from './';
import { setTiresByType } from "./tiresByType";
import { setTiresIndexed } from "./tiresIndexed";
import { addBrandsFilters } from "./filtersBrands";
import { setTiresByBrand } from './tiresByBrand';

const SET_TIRES_BY_SHOP_ID = "SET_TIRES_BY_SHOP_ID";

export interface ITiresByShopId {
  [key: string]: number[];
}

interface ISetTiresByShopIdAction {
  type: string;
  payload: ITiresByShopId;
}

export const setTiresByShopId = (tiresByShopId: ITiresByShopId) => ({
  type: SET_TIRES_BY_SHOP_ID,
  payload: tiresByShopId,
});

export const indexTiresByShopID = () => (dispatch, getState: () => IState): void => {
  const state = getState();
  const tiresObj = {};
  const shopsObj = {};
  const typeObj = {
    All: [],
    Trailer: [],
    Drive: [],
    Steer: []
  };
  const brandsObj = {};
  const brandObjWithIds = {};

  state.tires.forEach((tire) => {
    tiresObj[tire.id] = tire;
    if (shopsObj[tire.shopId]) {
      shopsObj[tire.shopId] = shopsObj[tire.shopId].concat([tire.id]);
    } else {
      shopsObj[tire.shopId] = [tire.id];
    }
    if (typeObj[tire.type]) {
      typeObj[tire.type] = typeObj[tire.type].concat([tire.id]);
    } else {
      typeObj[tire.type] = [tire.id];
    }
    if (brandObjWithIds[tire.brand]) {
      brandObjWithIds[tire.brand] = brandObjWithIds[tire.brand].concat([tire.id]);
    } else {
      brandObjWithIds[tire.brand] = [tire.id]
    }
    if (!brandsObj[tire.brand])
      brandsObj[tire.brand] = { value: tire.brand, isChecked: false };
  });

  console.log("BRANDS IDS", brandObjWithIds)

  // dispatch(addBrandsFilters(Object.keys(brandsObj)))
  dispatch(addBrandsFilters(brandsObj));
  dispatch(setTiresIndexed(tiresObj));
  dispatch(setTiresByType(typeObj));
  dispatch(setTiresByShopId(shopsObj));
  dispatch(setTiresByBrand(brandObjWithIds));
};

export default (state: ITiresByShopId = {}, action: ISetTiresByShopIdAction) => {
  switch (action.type) {
    case SET_TIRES_BY_SHOP_ID:
      return action.payload;
    default:
      return state;
  }
};
