import React from 'react';
import { connect } from 'react-redux';
import {
  IState,
  EnumDropdowns,
  setSelectedDropdown,
  changeOpen247,
  changeOpen247Mobile,
} from '../../../store';

interface IMapState {
  selectedDropdown: string;
  open247Selected: boolean;
  open247MobileSelected: boolean;
}

interface IMapDispatch {
  setSelectedDropdownDispatch: (s: string) => void;
  changeOpen247Dispatch: () => void;
  changeOpen247MobileDispatch: () => void;
}

interface IFilterAfterHours extends IMapState, IMapDispatch {}

function FiltersAfterHours(props: IFilterAfterHours): JSX.Element {
  const {
    selectedDropdown,
    setSelectedDropdownDispatch,
    open247Selected,
    open247MobileSelected,
    changeOpen247Dispatch,
    changeOpen247MobileDispatch,
  } = props;
  let btnCSS = 'filters-serviceDropBtn dropdown__btn';
  let dropCSS = 'invis';
  if (selectedDropdown === EnumDropdowns.OPEN_247) {
    btnCSS = 'filters-sortDropBtn dropdown__btn dropdown__btn-active';
    dropCSS = 'dropdown__content';
  }
  const handleClick = () => {
    if (selectedDropdown === EnumDropdowns.OPEN_247) {
      setSelectedDropdownDispatch(EnumDropdowns.NONE);
    } else {
      setSelectedDropdownDispatch(EnumDropdowns.OPEN_247);
    }
  };
  return (
    <div className='filters-afterHours'>
      <button className={btnCSS} onClick={() => handleClick()}>
        {/* Service */}
        24/7&nbsp;
      </button>
      <div data-testid='filters-afterHours-dropdown' className={dropCSS}>
        <label className='checkbox__label'>
          <input
            type='checkbox'
            className='checkbox__input'
            value={'open247Selected'}
            checked={open247Selected}
            onChange={() => changeOpen247Dispatch()}
            data-testid='filter-afterHours-inShop-btn'
          />
          <span className='checkbox__custom'></span>
          24/7 In Shop&nbsp;
        </label>
        <label className='checkbox__label'>
          <input
            type='checkbox'
            className='checkbox__input'
            value={'open247MobileSelected'}
            checked={open247MobileSelected}
            onChange={() => changeOpen247MobileDispatch()}
            data-testid='filter-afterHours-roadService-btn'
          />
          <span className='checkbox__custom'></span>
          24/7 Road Service&nbsp;
        </label>
      </div>
    </div>
  );
}

const mapState = (state: IState) => {
  return {
    selectedDropdown: state.selectedDropdown,
    open247Selected: state.open247Selected,
    open247MobileSelected: state.open247MobileSelected,
  };
};

const mapDispatch = (dispatch) => {
  return {
    setSelectedDropdownDispatch(sd: string) {
      dispatch(setSelectedDropdown(sd));
    },
    changeOpen247Dispatch() {
      dispatch(changeOpen247());
    },
    changeOpen247MobileDispatch() {
      dispatch(changeOpen247Mobile());
    },
  };
};

export default connect(mapState, mapDispatch)(FiltersAfterHours);
