import React from 'react';
import { connect } from 'react-redux';
import {
  IState,
  ITire,
  ITireDenormalized,
  // IShopsIndexed,
  setSelectedTireById,
} from '../../../store';
import SVGNoImage from '../svg/SVGNoImage';
import SVGTire from '../svg/SVGTire';

interface IMapState {
  // shopsIndexed: IShopsIndexed;
}

interface IMapDispatch {
  setSelectedTireByIdDispatch: (id: number) => void;
}

interface IShopInfoTireCardProps extends ITireDenormalized, IMapDispatch {
  displayDistance: boolean;
  displayFinalPrice: boolean;
  testParent: string;
}

function ShopInfoTireCard(props: IShopInfoTireCardProps): JSX.Element {
  const {
    id,
    brand,
    model,
    beforeDiscountPrice,
    netPrice,
    finalOutTheDoorPrice,
    mounting,
    disposal,
    imgLink,
    type,
    distance,
    displayDistance,
    setSelectedTireByIdDispatch,
    displayFinalPrice,
    testParent,
  } = props;

  let tagClass = '';
  let theme = 'dark';
  if (type === 'Drive')
    tagClass = `tct-${theme}-tags-tag tct-${theme}-tags-tag-drive`;
  if (type === 'Steer')
    tagClass = `tct-${theme}-tags-tag tct-${theme}-tags-tag-steer`;
  if (type === 'Trailer')
    tagClass = `tct-${theme}-tags-tag tct-${theme}-tags-tag-trailer`;
  if (type === 'All')
    tagClass = `tct-${theme}-tags-tag tct-${theme}-tags-tag-all`;
  let CLASS = 'tires-card-tires-tags-tag ' + tagClass;

  const handleClick = (id) => {
    setSelectedTireByIdDispatch(id);
  };

  // console.log('ID', id);

  let PRICE_CALC;
  if (netPrice && netPrice > 0 && !finalOutTheDoorPrice) {
    PRICE_CALC = netPrice;
    if (mounting && mounting > 0) {
      PRICE_CALC = PRICE_CALC + mounting;
    }
    if (disposal && disposal > 0) {
      PRICE_CALC = PRICE_CALC + disposal;
    }
    PRICE_CALC = parseFloat(PRICE_CALC).toFixed(2);
  } else {
    PRICE_CALC = finalOutTheDoorPrice;
  }

  return (
    <div
      className={`tires-card-tires home-tire-card tct-${theme}`}
      // className='tires-card shop-info-window-tires-card'
      // shopInfo-tireCard-shopModal
      data-testid={`shopInfo-tireCard-${testParent}`}
      onClick={() => handleClick(id)}
    >
      <div className={`tires-card-tires-tags tct-${theme}-tags`}>
        <div className={CLASS}>{type}&nbsp;</div>
        {displayDistance && (
          <div className={`tires-card-tires-tags-dist tct-${theme}-tags-dist`}>
            {distance}mi&nbsp;
          </div>
        )}
      </div>
      {/* {imgLink !== '' ? (
        <img className='shop-info-window-tires-card-img' src={imgLink}></img>
      ) : (
        <SVGNoImage />
      )} */}
      <img
        className='tires-card-tires-img'
        src={`/tires/${type}-axle-position-commercial-truck-tire.png`}
        alt={
          model
            ? `${type} Position ${brand} ${model} Tire`
            : `${type} Position ${brand} Tire`
        }
      ></img>
      {/* <SVGTire tireType={type} /> */}
      <div className={`tires-card-tires-title tct-${theme}-title`}>
        <h2 className={`tires-card-tires-title-brand tct-${theme}-title-brand`}>
          {brand}
        </h2>
        <h2 className={`tires-card-tires-title-model tct-${theme}-title-model`}>
          {model ? model : 'Model Unspecified'}&nbsp;
        </h2>
      </div>
      {/* <h2 className="tires-card-title list-item-tires-card-title">{model}</h2> */}
      <p className={`tires-card-tires-price tct-${theme}-price`}>
        <span className={`tires-card-tires-price-old tct-${theme}-price-old`}>
          {netPrice !== 0 ? `$${beforeDiscountPrice}` : 'Final'}&nbsp;
        </span>{' '}
        ${netPrice !== 0 ? netPrice : PRICE_CALC}&nbsp;
      </p>
      {displayFinalPrice && (
        <React.Fragment>
          {finalOutTheDoorPrice ? (
            <p
              className={`tires-card-tires-price-final tct-${theme}-price-final`}
            >
              Out The Door{' '}&nbsp;
              <span
                className={`tires-card-tires-price-final-num tct-${theme}-price-final-num`}
              >
                ${finalOutTheDoorPrice.toFixed(2)}&nbsp;
              </span>
            </p>
          ) : (
            <p
              className={`tires-card-tires-price-final tct-${theme}-price-final`}
            >
              Installed Est{' '}
              <span
                className={`tires-card-tires-price-final-num tct-${theme}-price-final-num`}
              >
                ${(netPrice + mounting + disposal).toFixed(2)}&nbsp;
              </span>
            </p>
          )}
        </React.Fragment>
      )}
    </div>
  );
}

const mapState = (state: IState): IMapState => {
  return {
    // shopsIndexed: state.shopsIndexed,
  };
};

const mapDispatch = (dispatch): IMapDispatch => {
  return {
    setSelectedTireByIdDispatch(tireId) {
      dispatch(setSelectedTireById(tireId));
    },
  };
};

export default connect(mapState, mapDispatch)(ShopInfoTireCard);
