import React from 'react';
import { connect } from 'react-redux';
import ShopInfoTireCard from './ShopInfoTireCard';
import {
  IState,
  IShop,
  ITiresByShopId,
  ITiresIndexed,
  EnumLayouts,
  setSelectedTireById,
  setSelectedShopById,
  changeIsModalOpen,
} from '../../../store';
import SVGPhone from '../svg/SVGPhone';
import { parsePhoneNumber } from 'libphonenumber-js';

interface IShopInfoWindowMapState {
  selectedShop: IShop[];
  tiresByShopId: ITiresByShopId;
  tiresIndexed: ITiresIndexed;
  selectedLayout: EnumLayouts;
}

interface IProps extends IShopInfoWindowMapState {
  setSelectedTireByIdDispatch: (id: string | number) => any;
  setSelectedShopByIdDispatch: (id: string | number) => any;
  changeIsModalOpenDispatch: () => void;
}

function ShopInfoWindow(props: IProps) {
  const {
    selectedShop,
    tiresByShopId,
    tiresIndexed,
    selectedLayout,
    setSelectedTireByIdDispatch,
    setSelectedShopByIdDispatch,
    changeIsModalOpenDispatch,
  } = props;
  const {
    id,
    name,
    address,
    lat,
    lng,
    distance,
    roadService,
    phone,
    inShopService,
    phoneAfterHours,
    mounting,
    mountingAfterHours,
    mobileFee,
    mobileFeeAfterHours,
    mobilePerHour,
    mobilePerHourAfterHours,
    mobilePerMile,
    mobilePerMileAfterHours,
    open247,
    open247Mobile,
    websiteName,
    hours,
    mobileHourMin,
    mobileHourMinAfterHours,
  } = selectedShop[0];
  let roadServiceShopRate;
  if (roadService) {
    roadServiceShopRate = '';
    if (mobileFee) {
      roadServiceShopRate += `$${mobileFee} + `;
    }
    if (mobilePerHour) {
      if (mobileHourMin) {
        roadServiceShopRate += `${mobileHourMin} hour min `;
      }
      roadServiceShopRate += `$${mobilePerHour} per hour `;
    }
    if (mobilePerMile) {
      roadServiceShopRate += `$${mobilePerMile} per mile`;
    }
  }
  const phoneParsed = parsePhoneNumber(phone, 'US');
  const phoneInter = phoneParsed.formatNational();

  const phoneDigits = phone.toString().replace(/\D/g, '');
  let phoneDigitsAfter;
  if (phoneAfterHours)
    phoneDigitsAfter = phoneAfterHours.toString().replace(/\D/g, '');
  // console.log('SELECTED SHOP ', selectedShop);
  const shopTiresIDS = tiresByShopId[id];
  // console.log('TIRES BY SHOP ID', tiresByShopId);
  // console.log('TIRES SHOP ', id, shopTiresIDS);

  let shopInfoWindowCSS = 'shop-info-window';
  if (selectedLayout === EnumLayouts.MAP) {
    shopInfoWindowCSS = 'shop-info-window shop-info-window-map';
  }

  const handleInfoClick = (shopId) => {
    setSelectedTireByIdDispatch(tiresByShopId[shopId][0]);
    setSelectedShopByIdDispatch(shopId);
    changeIsModalOpenDispatch();
  };

  return (
    <div className={shopInfoWindowCSS} data-testid='shop-info-window-component'>
      <div className='shop-info-window__title'>
        <h3
          className='shop-info-window__title-inner'
          onClick={() => handleInfoClick(id)}
        >
          {name}
        </h3>
        <span className='shop-info-window__title-distance'>
          {distance} mi away
        </span>
      </div>
      <div className='shop-info-window__address'>{address}</div>
      <p className='shopInfoModal-info__phone'>
        <SVGPhone />
        <a href={'tel:' + phoneDigits}>{phoneInter}</a>
      </p>
      {inShopService && (
        <React.Fragment>
          {/* <div className='shopInfoModal-info-shopRates-title'>
            <h3 className='shopInfoModal-info-shopRates-title-inner'>
              Shop Rates
            </h3>
          </div> */}
          {/* <p className='shopInfoModal-info__inShopRate'>
            <SVGPrice />
            Mounting: {mounting ? `$${mounting}` : 'Included in Price'}
          </p> */}
        </React.Fragment>
      )}
      {roadService && (
        <React.Fragment>
          {/* <div className='shopInfoModal-info-roadServiceRates'>
            <p className='shopInfoModal-info__roadServiceRate'>
              <SVGPrice />
              Road Service:{' '}
              {roadServiceShopRate
                ? roadServiceShopRate
                : 'Rates Not Specified, Please Call'}
            </p>
          </div> */}
        </React.Fragment>
      )}
      <div className='shop-info-window-tires'>
        {shopTiresIDS &&
          shopTiresIDS.map(
            (tireID: number): JSX.Element => {
              const tire = tiresIndexed[tireID];
              return (
                <ShopInfoTireCard
                  key={tire.id}
                  id={tire.id}
                  shopId={tire.shopId}
                  brand={tire.brand}
                  model={tire.model}
                  beforeDiscountPrice={tire.beforeDiscountPrice}
                  netPrice={tire.netPrice}
                  finalOutTheDoorPrice={tire.finalOutTheDoorPrice}
                  mounting={tire.mounting}
                  imgLink={tire.imgLink}
                  type={tire.type}
                  distance={distance}
                  displayDistance={false}
                  displayFinalPrice={false}
                />
              );
            }
          )}
      </div>
    </div>
  );
}

const mapState = (state: IState): IShopInfoWindowMapState => {
  return {
    selectedShop: state.selectedShop,
    tiresByShopId: state.tiresByShopId,
    tiresIndexed: state.tiresIndexed,
    selectedLayout: state.selectedLayout,
  };
};

const MapDispatch = (dispatch) => {
  return {
    setSelectedTireByIdDispatch(id) {
      dispatch(setSelectedTireById(id));
    },
    setSelectedShopByIdDispatch(id) {
      dispatch(setSelectedShopById(id));
    },
    changeIsModalOpenDispatch() {
      dispatch(changeIsModalOpen());
    },
  };
};

export default connect(mapState, MapDispatch)(ShopInfoWindow);
