const SET_ALL_SELECTED = "SET_ALL_SELECTED";
const SET_STEER_SELECTED = "SET_STEER_SELECTED";
const SET_DRIVE_SELECTED = "SET_DRIVE_SELECTED";
const SET_TRAILER_SELECTED = "SET_TRAILER_SELECTED";
const CHANGE_ALL_SELECTED = "CHANGE_ALL_SELECTED";
const CHANGE_STEER_SELECTED = "CHANGE_STEER_SELECTED";
const CHANGE_DRIVE_SELECTED = "CHANGE_DRIVE_SELECTED";
const CHANGE_TRAILER_SELECTED = "CHANGE_TRAILER_SELECTED";

export interface IFilterObject {
  value: string;
  label: string;
  isChecked: boolean;
  category: string;
  id: number;
}

export interface IFiltersType {
  all: IFilterObject;
  steer: IFilterObject;
  drive: IFilterObject;
  trailer: IFilterObject;
}

export const setAllSelected = () => ({
  type: SET_ALL_SELECTED
});

export const setSteerSelected = () => ({
  type: SET_STEER_SELECTED
});

export const setDriveSelected = () => ({
  type: SET_DRIVE_SELECTED
});

export const setTrailerSelected = () => ({
  type: SET_TRAILER_SELECTED
});

export const changeAllSelected = () => ({
  type: CHANGE_ALL_SELECTED
});

export const changeSteerSelected = () => ({
  type: CHANGE_STEER_SELECTED
});

export const changeDriveSelected = () => ({
  type: CHANGE_DRIVE_SELECTED
});

export const changeTrailerSelected = () => ({
  type: CHANGE_TRAILER_SELECTED
});

