const filtersObject = {
  all: {
    value: "All",
    label: "All",
    isChecked: false,
    category: "type",
    id: 1
  },
  steer: {
    value: "Steer",
    label: "Steer",
    isChecked: false,
    category: "type",
    id: 2
  },
  drive: {
    value: "Drive",
    label: "Drive",
    isChecked: false,
    category: "type",
    id: 3
  },
  trailer: {
    value: "Trailer",
    label: "Trailer",
    isChecked: false,
    category: "type",
    id: 4
  }
};

export default (state: IFiltersType = filtersObject, action) => {
  const newState = { ...state };
  switch (action.type) {
    case SET_ALL_SELECTED:
      newState.all.isChecked = true;
      return newState;
    case SET_STEER_SELECTED:
      newState.steer.isChecked = true;
      return newState;
    case SET_DRIVE_SELECTED:
      newState.drive.isChecked = true;
      return newState;
    case SET_TRAILER_SELECTED:
      newState.trailer.isChecked = true;
      return newState;
    case CHANGE_ALL_SELECTED:
      newState.all.isChecked = !newState.all.isChecked;
      return newState;
    case CHANGE_STEER_SELECTED:
      newState.steer.isChecked = !newState.steer.isChecked;
      return newState;
    case CHANGE_DRIVE_SELECTED:
      newState.drive.isChecked = !newState.drive.isChecked;
      return newState;
    case CHANGE_TRAILER_SELECTED:
      newState.trailer.isChecked = !newState.trailer.isChecked;
      return newState;
    default:
      return state;
  }
};
