/* eslint-disable jsx-a11y/label-has-associated-control */
import React from 'react';
import { connect } from 'react-redux';
import {
  IState,
  EnumDropdowns,
  setSelectedDropdown,
  changeInShopServiceSelected,
  changeRoadServiceSelected,
} from '../../../store';

interface IMapStateProps {
  selectedDropdown: string;
  inShopServiceSelected: boolean;
  roadServiceSelected: boolean;
}

interface IMapDispatchProps {
  setSelectedDropdownDispatch: (s: string) => void;
  changeInShopServiceDispatch: () => void;
  changeRoadServiceSelectedDispatch: () => void;
}

interface IFilterServiceProps extends IMapStateProps, IMapDispatchProps {}

function FiltersService(props: IFilterServiceProps): JSX.Element {
  const {
    selectedDropdown,
    setSelectedDropdownDispatch,
    roadServiceSelected,
    inShopServiceSelected,
    changeRoadServiceSelectedDispatch,
    changeInShopServiceDispatch,
  } = props;
  // const [dropSelected, setDropSelected] = useState<boolean>(false);
  let btnCSS = 'filters-serviceDropBtn dropdown__btn';
  let dropCSS = 'invis';
  if (selectedDropdown === EnumDropdowns.MOBILITY) {
    btnCSS = 'filters-sortDropBtn dropdown__btn dropdown__btn-active';
    dropCSS = 'dropdown__content';
  }
  const handleClick = () => {
    if (selectedDropdown === EnumDropdowns.MOBILITY) {
      setSelectedDropdownDispatch(EnumDropdowns.NONE);
    } else {
      setSelectedDropdownDispatch(EnumDropdowns.MOBILITY);
    }
  };
  return (
    <div className='filters-mobility'>
      {/* <button className={btnCSS} onClick={() => setDropSelected(!dropSelected)}> */}
      <button className={btnCSS} onClick={() => handleClick()}>
        Mobility&nbsp;
      </button>
      <div data-testid='filters-mobility-dropdown' className={dropCSS}>
        <label className='checkbox__label'>
          <input
            type='checkbox'
            className='checkbox__input'
            value={'roadService'}
            checked={roadServiceSelected}
            onChange={() => changeRoadServiceSelectedDispatch()}
            data-testid='filters-mobility-roadService-btn'
          />
          <span className='checkbox__custom'></span>
          Road Service&nbsp;
        </label>
        <label className='checkbox__label'>
          <input
            type='checkbox'
            className='checkbox__input'
            value={'inShopService'}
            checked={inShopServiceSelected}
            onChange={() => changeInShopServiceDispatch()}
            data-testid='filters-mobility-inShop-btn'
          />
          <span className='checkbox__custom'></span>
          In Shop&nbsp;
        </label>
        {/* <div className='filters__afterServiceDropdown'>
          <label className='checkbox__label'>
            <input
              type='checkbox'
              className='checkbox__input'
              value={''}
              checked={false}
              onChange={() => console.log('after hours btn')}
              data-testid='afterHoursService-filter-btn'
            />
            <span className='checkbox__custom'></span>
            After Hours
          </label>
        </div> */}
      </div>
    </div>
  );
}

const mapState = (state: IState) => {
  return {
    selectedDropdown: state.selectedDropdown,
  };
};

const mapDispatch = (dispatch) => {
  return {
    setSelectedDropdownDispatch(sd: string) {
      dispatch(setSelectedDropdown(sd));
    },
    changeRoadServiceSelectedDispatch() {
      dispatch(changeRoadServiceSelected());
    },
    changeInShopServiceDispatch() {
      dispatch(changeInShopServiceSelected());
    },
  };
};

export default connect(mapState, mapDispatch)(FiltersService);
