import React, { useState, MouseEvent } from 'react';
import { connect } from 'react-redux';
import {
  setSelectedTire,
  setSelectedTireById,
  setSelectedShopById,
  changeIsModalOpen,
} from '../../../store';
import { IState, ITireDenormalized, IShopsIndexed } from '../../../store';
import SVGTire from '../svg/SVGTire';

interface ITireCardTiresMapState {
  shopsIndexed: IShopsIndexed;
  filtersSize: any;
}

interface ITireCardTiresMapDispatch {
  setSelectedTire: (tire: ITireDenormalized) => void;
  setSelectedTireById: (id: number) => void;
  setSelectedShopById: (id: number | string) => void;
  changeIsModalOpenDispatch: () => void;
}

interface ITireCardTireProps
  extends ITireDenormalized,
    ITireCardTiresMapState,
    ITireCardTiresMapDispatch {
  displayDistance: boolean;
  displayShortName: boolean;
  testParent: string;
}

// const TireCardTires: React.FC<ITireCardTireProps> = (props) => {
function TireCardTires(props: ITireCardTireProps): JSX.Element {
  let { beforeDiscountPrice } = props;
  const {
    id,
    shopId,
    brand,
    model,
    netPrice,
    finalOutTheDoorPrice,
    mounting,
    disposal,
    imgLink,
    type,
    shopsIndexed,
    setSelectedTireById,
    setSelectedShopById,
    changeIsModalOpenDispatch,
    displayDistance,
    displayShortName,
    roadService,
    inShopService,
    mobileFee,
    mobilePerHour,
    mobilePerMile,
    mobileHourMin,
    testParent,
    filtersSize,
    size,
  } = props;

  let roadServiceShopRate;
  if (roadService) {
    roadServiceShopRate = '';
    if (mobileFee) {
      roadServiceShopRate += `$${mobileFee} + `;
    }
    if (mobilePerHour) {
      if (mobileHourMin) {
        roadServiceShopRate += `${mobileHourMin}h min `;
      }
      roadServiceShopRate += `$${mobilePerHour}/h `;
    }
    if (mobilePerMile) {
      roadServiceShopRate += `$${mobilePerMile}/mi`;
    }
  }

  let tagClass = '';
  let theme = 'dark';
  if (type === 'Drive')
    tagClass = `tct-${theme}-tags-tag tct-${theme}-tags-tag-drive`;
  if (type === 'Steer')
    tagClass = `tct-${theme}-tags-tag tct-${theme}-tags-tag-steer`;
  if (type === 'Trailer')
    tagClass = `tct-${theme}-tags-tag tct-${theme}-tags-tag-trailer`;
  if (type === 'All')
    tagClass = `tct-${theme}-tags-tag tct-${theme}-tags-tag-all`;
  let CLASS = 'tires-card-tires-tags-tag ' + tagClass;
  // console.log("TYPE ", type, "TAG ", tagClass);

  const handleClick = (id) => {
    setSelectedTireById(id);
    setSelectedShopById(shopId);
    changeIsModalOpenDispatch();
  };

  let TEST_ID = 'tires-card-component';
  if (testParent) {
    // tires-card-component-shopModal
    TEST_ID = `tires-card-component-${testParent}`;
  }

  if (netPrice > 0) {
    let randomPercent: number[] = [0.12, 0.15, 0.17, 0.2, 0.25, 0.28];
    if (Number(beforeDiscountPrice) === 0) {
      let ranIdx = Math.floor(Math.random() * (randomPercent.length - 1)) + 1;
      beforeDiscountPrice = netPrice + netPrice * randomPercent[ranIdx];
      beforeDiscountPrice = parseFloat(beforeDiscountPrice).toFixed(2);
    }
  }

  console.log('BEFORE DISCOUNT PRICE ', beforeDiscountPrice);

  let PRICE_CALC;
  if (netPrice && netPrice > 0 && !finalOutTheDoorPrice) {
    PRICE_CALC = netPrice;
    if (mounting && mounting > 0) {
      PRICE_CALC = PRICE_CALC + mounting;
    }
    if (disposal && disposal > 0) {
      PRICE_CALC = PRICE_CALC + disposal;
    }
    PRICE_CALC = parseFloat(PRICE_CALC).toFixed(2);
  } else {
    PRICE_CALC = finalOutTheDoorPrice;
  }

  return (
    <div
      className={`tires-card-tires tct-${theme}`}
      data-testid={TEST_ID}
      onClick={(event: MouseEvent<HTMLDivElement>) => handleClick(id)}
    >
      {displayShortName && (
        <div className='tires-card-tires-shopName-wrap'>
          <h3 className={`tires-card-tires-shopName tct-${theme}-shopName`}>
            {shopsIndexed[shopId].shortName}&nbsp;
          </h3>
        </div>
      )}
      <div className={`tires-card-tires-tags tct-${theme}-tags`}>
        <div className={CLASS} data-testid='type-tag'>
          {type}&nbsp;
        </div>
        {displayDistance && (
          <div className={`tires-card-tires-tags-dist tct-${theme}-tags-dist`}>
            {shopsIndexed[shopId].distance}mi&nbsp;
          </div>
        )}
      </div>
      {/* {imgLink !== '' ? (
        <img className='tires-card-tires-img' src={imgLink}></img>
      ) : (
        <SVGNoImage />
      )} */}
      {/* <SVGNoImage /> */}
      <div className='tires-card-tires-sizeTag'>
        {filtersSize[size].label}&nbsp;
      </div>
      {/* <img
        className='tires-card-tires-img'
        src={`/tires/${type}-axle-position-commercial-truck-tire.png`}
        alt={
          model
            ? `${type} Position ${brand} ${model} Tire`
            : `${type} Position ${brand} Tire`
        }
      ></img> */}

      <SVGTire tireType={type} />
      <div className={`tires-card-tires-title tct-${theme}-title`}>
        <h2
          className={`tires-card-tires-title-brand tct-${theme}-title-brand`}
          data-testid='tires-card-brand-title'
        >
          {brand}&nbsp;
        </h2>
        <h2 className={`tires-card-tires-title-model tct-${theme}-title-model`}>
          {model ? model : 'Model Unspecified'}&nbsp;
        </h2>
        <h2 className={`tires-card-tires-title-axle tct-${theme}-title-axle`}>
          {type === 'All' ? `${type} Position Tire` : `${type} Axle Tire`}&nbsp;
        </h2>
      </div>
      <p className={`tires-card-tires-price tct-${theme}-price`}>
        <span className={`tires-card-tires-price-old tct-${theme}-price-old`}>
          {netPrice !== 0 ? `$${beforeDiscountPrice}` : `Final`}&nbsp;
        </span>{' '}
        ${netPrice !== 0 ? netPrice : PRICE_CALC}&nbsp;
      </p>
      <p className={`tires-card-tires-price-final tct-${theme}-price-final`}>
        {inShopService && !finalOutTheDoorPrice && `Shop Est. `}
        {inShopService && finalOutTheDoorPrice !== 0 && `Final Est. `}
        {inShopService && (
          <span
            className={`tires-card-tires-price-final-num tct-${theme}-price-final-num`}
          >
            {`$${PRICE_CALC}`}&nbsp;
          </span>
        )}
        {!inShopService && `Road Service Only`}&nbsp;
      </p>
      <p
        className={`tires-card-tires-price-final-mobile tct-${theme}-price-final-mobile`}
      >
        {roadService && roadServiceShopRate && `Mobile `}
        {/* {inShopService && roadService && `Mobile `} */}
        {inShopService &&
          roadService &&
          !roadServiceShopRate &&
          `Mobile Available`}
        &nbsp;
        {roadService && (
          <span
            className={`tires-card-tires-price-final-mobile-num tct-${theme}-price-final-mobile-num`}
          >
            {roadServiceShopRate}&nbsp;
          </span>
        )}
        {!roadService && `In Shop Service `}
      </p>
    </div>
  );
}

const mapState = (state: IState): ITireCardTiresMapState => {
  return {
    shopsIndexed: state.shopsIndexed,
    filtersSize: state.filtersSize,
  };
};

const mapDispatch = (dispatch): ITireCardTiresMapDispatch => {
  return {
    setSelectedTire(tire) {
      dispatch(setSelectedTire(tire));
    },
    setSelectedTireById(id) {
      dispatch(setSelectedTireById(id));
    },
    setSelectedShopById(id) {
      dispatch(setSelectedShopById(id));
    },
    changeIsModalOpenDispatch() {
      dispatch(changeIsModalOpen());
    },
  };
};

export default connect(mapState, mapDispatch)(TireCardTires);
