import { IShop } from './shops';

const SELECTED_SHOP = "SELECTED_SHOP";

interface ISetSelectedShopAction {
  type: string;
  payload: IShop;
}

export const setSelectedShop = (shop: IShop): ISetSelectedShopAction => ({
  type: SELECTED_SHOP,
  payload: shop
});

export const setSelectedShopById = (shopId) => (dispatch, getState) => {
  // console.log("SHOP ID", shopId)
  const state = getState();
  // let shop = state.shops.filter(s=> s.id === shopId)[0];
  let shop = state.shopsIndexed[shopId]
  // console.log("SHOP IN ACTION", shop)
  dispatch(setSelectedShop(shop));
}

export default (state: IShop[] = [], action: ISetSelectedShopAction) => {
  switch (action.type) {
    case SELECTED_SHOP:
      return [action.payload];
    default:
      return state;
  }
};