import axios from 'axios';
import { getDistance } from '../../utils';
import { Dispatch } from 'redux';
import { IState } from './';

export interface IShop {
  id: number;
  name: string;
  shortName: string;
  address: string;
  phone: string;
  phoneAfterHours: string | null;
  lat: number;
  lng: number;
  roadService: boolean;
  inShopService: boolean;
  open247: boolean;
  open247Mobile: boolean;
  mounting: number | null;
  mountingAfterHours: number | null;
  balancing: number;
  disposal: number;
  mobileFee: number | null;
  mobileFeeAfterHours: number | null;
  mobilePerHour: number | null;
  mobilePerHourAfterHours: number | null;
  mobilePerMile: number | null;
  mobilePerMileAfterHours: number | null;
  websiteName?: string | null;
  hours: number | null;
  mobileHourMin: number | null;
  mobileHourMinAfterHours: number | null;
  valveStem: number;
  serviceInfo: string;
  distance?: number;
}

// enum ShopActionTypes {
//   setShops,
// }

interface ISetShopsAction {
  type: string;
  payload: IShop[];
}

const SET_SHOPS = "SET_SHOPS";

export const setShops = (shops: IShop[]): ISetShopsAction => ({
  type: SET_SHOPS,
  payload: shops,
});

export const setShopsWithDistance = (shops: IShop[]) => (dispatch: Dispatch, getState: () => IState) => {
  const state = getState();
  const { center } = state;
  const shopsWithDistance = shops.map((shop: IShop) => {
    const distance = getDistance(center, shop);
    return {
      ...shop,
      distance: distance,
    };
  });
  dispatch<ISetShopsAction>(setShops(shopsWithDistance));
};

export const fetchShops = () => async (dispatch: Dispatch, getState) => {
  // const shops = await axios.get("https://aidride-c5708.web.app/api/shops");
  const shops = await axios.get<IShop[]>(
    'https://nw61tmryv9.execute-api.us-east-2.amazonaws.com/Dev/shops'
  );
  // dispatch(setShops(shops.data));
  // dispatch<ISetShopsAction>({
  //   type: ShopActionTypes.setShops,
  //   shops: shops.data
  // })
  dispatch<ISetShopsAction>(setShops(shops.data));
};

export default (state: IShop[] = [], action: ISetShopsAction) => {
  switch (action.type) {
    case SET_SHOPS:
      return action.payload;
    default:
      return state;
  }
};
