const SET_CENTER = "SET_CENTER";

export interface ICenter {
  address: string;
  lat: number;
  lng: number;
  cityName?: string
}

interface ISetCenterAction {
  type: string;
  payload: ICenter;
}

export const setCenter = (center: ICenter): ISetCenterAction => ({
  type: SET_CENTER,
  payload: center
})

