const center = {
  address: "",
  lat: 0,
  lng: 0,
  cityName: ''
}

export default (state: ICenter = center, action: ISetCenterAction) => {
  switch (action.type) {
    case SET_CENTER:
      return action.payload;
    default:
      return state;
  }
};