import React, { useEffect } from 'react';
import { connect } from 'react-redux';
// import ListItem from "./ListItem";
import TireCardTires from './TireCardTires';
import FiltersType from '../filters/FiltersType';
import {
  IState,
  ICenter,
  IShopsIndexed,
  ITire,
  ITireDenormalized,
  ITiresByType,
  ITiresIndexed,
  ITiresByBrand,
  IFilterBrandObject,
  setSelectedShopById,
  EnumLayouts,
  setFilteredTires,
  setFilteredShopsIDS,
  IFilterSizeObject,
  ITiresBySize,
  // setDataLoading,
} from '../../../store';

interface IListTiresMapToStateProps {
  lat?: number;
  lng?: number;
  center: ICenter;
  shopsIndexed: IShopsIndexed;
  tires: ITireDenormalized[];
  tiresIndexed: ITiresIndexed;
  tiresByType: ITiresByType;
  allIsChecked: boolean;
  steerIsChecked: boolean;
  driveIsChecked: boolean;
  trailerIsChecked: boolean;
  sortBy: string;
  tiresByBrand: ITiresByBrand;
  filtersBrands: IFilterBrandObject;
  filtersSize: IFilterSizeObject;
  isDataLoading: boolean;
  roadServiceSelected: boolean;
  inShopServiceSelected: boolean;
  open247Selected: boolean;
  open247MobileSelected: boolean;
  selectedLayout: EnumLayouts;
  tiresBySize: ITiresBySize;
}

interface IListTiresMapMapDispatchProps {
  setSelectedShopByIdDispatch: (selectedShop: any) => void;
  setFilteredTiresDispatch: (filteredTires: ITireDenormalized[]) => void;
  setFilteredShopsIDSDispatch: (s: string[]) => void;
  // setDataLoadingDispatch: (condition: boolean) => void;
}

interface ListTiresMapProps
  extends IListTiresMapToStateProps,
    IListTiresMapMapDispatchProps {}

function ListTiresMap(props: ListTiresMapProps): JSX.Element {
  let {
    lat,
    lng,
    center,
    shopsIndexed,
    tires,
    tiresIndexed,
    tiresByType,
    allIsChecked,
    steerIsChecked,
    driveIsChecked,
    trailerIsChecked,
    sortBy,
    tiresByBrand,
    filtersBrands,
    filtersSize,
    setSelectedShopByIdDispatch,
    isDataLoading,
    inShopServiceSelected,
    roadServiceSelected,
    open247Selected,
    open247MobileSelected,
    selectedLayout,
    setFilteredTiresDispatch,
    setFilteredShopsIDSDispatch,
    tiresBySize,
  } = props;

  let filteredTires = [];

  const filterTires = () => {
    let filteredTiresIDS = [];
    let filteredByType = [];
    let filteredByBrand = [];
    let filteredBySize = [];

    // if (allIsChecked)
    //   filteredTiresIDS = [...filteredTiresIDS, ...tiresByType['All']];
    // if (steerIsChecked)
    //   filteredTiresIDS = filteredTiresIDS.concat(tiresByType['Steer']);
    // if (driveIsChecked)
    //   filteredTiresIDS = filteredTiresIDS.concat(tiresByType['Drive']);
    // if (trailerIsChecked)
    //   filteredTiresIDS = filteredTiresIDS.concat(tiresByType['Trailer']);

    if (allIsChecked)
      filteredByType = [...filteredByType, ...tiresByType['All']];
    if (steerIsChecked)
      filteredByType = filteredByType.concat(tiresByType['Steer']);
    if (driveIsChecked)
      filteredByType = filteredByType.concat(tiresByType['Drive']);
    if (trailerIsChecked)
      filteredByType = filteredByType.concat(tiresByType['Trailer']);

    if (
      !allIsChecked &&
      !steerIsChecked &&
      !driveIsChecked &&
      !trailerIsChecked
    ) {
      filteredByType = [
        ...tiresByType['All'],
        ...tiresByType['Steer'],
        ...tiresByType['Drive'],
        ...tiresByType['Trailer'],
      ];
    }

    // filteredTiresIDS = filteredByType;

    Object.keys(filtersBrands).forEach((key) => {
      if (filtersBrands[key].isChecked) {
        filteredByBrand = [...filteredByBrand, ...tiresByBrand[key]];
      }
    });

    Object.keys(filtersSize).forEach((key) => {
      if (filtersSize[key].isChecked) {
        filteredBySize = [...filteredBySize, ...tiresBySize[key]];
      }
    });

    if (filteredByBrand.length) {
      // filteredTiresIDS = filteredTiresIDS.filter()
      filteredTiresIDS = filteredByType.filter((value) =>
        filteredByBrand.includes(value)
      );
    } else {
      // not needed
      filteredTiresIDS = filteredByType;
    }

    if (filteredBySize.length) {
      filteredTiresIDS = filteredTiresIDS.filter((value) =>
        filteredBySize.includes(value)
      );
      // let test = filteredTiresIDS.filter((value) =>
      //   filteredBySize.includes(value)
      // );
      // console.log('FILTERED SIZE ', filteredBySize);
      // console.log('FIltered IDS ', filteredTiresIDS);
      // console.log('include fn res', test);
    }

    // let brandFilterSelected = false;
    // Object.keys(filtersBrands).forEach((key) => {
    //   if (filtersBrands[key].isChecked) {
    //     // find common elements with an array of Type and current Brand
    //     if (tiresByBrand[key] && tiresByBrand[key].length) {
    //       brandFilterSelected = true;
    //       let filteredByBrand = filteredByType.filter((id) =>
    //         tiresByBrand[key].includes(id)
    //       );
    //       filteredTiresIDS = [...filteredTiresIDS, ...filteredByBrand];
    //     }
    //   }
    // });

    // if (!brandFilterSelected) filteredTiresIDS = filteredByType;

    filteredTires = filteredTiresIDS.map((tireID) => tiresIndexed[tireID]);

    // if (!filteredTires.length) filteredTires = tires;

    if (sortBy === 'price') {
      filteredTires = filteredTires.sort((a, b) => {
        // let arg1 = 'finalOutTheDoorPrice';
        // if (!a[arg1]) arg1 = 'netPrice';
        // let arg2 = 'finalOutTheDoorPrice';
        // if (!b[arg2]) arg2 = 'netPrice';
        // return a[arg1] - b[arg2];
        let priceOne;
        if (a.finalOutTheDoorPrice) {
          priceOne = a.finalOutTheDoorPrice;
        } else {
          priceOne = a.netPrice;
          if (a.mounting) priceOne = priceOne + a.mounting;
          if (a.disposal) priceOne = priceOne + a.disposal;
        }
        let priceTwo;
        if (b.finalOutTheDoorPrice) {
          priceTwo = b.finalOutTheDoorPrice;
        } else {
          priceTwo = b.netPrice;
          if (b.mounting) priceTwo = priceTwo + b.mounting;
          if (b.disposal) priceTwo = priceTwo + b.disposal;
        }
        return priceOne - priceTwo;
      });
    }
    if (sortBy === 'distance') {
      filteredTires = filteredTires.sort(
        (a, b) =>
          shopsIndexed[a.shopId].distance - shopsIndexed[b.shopId].distance
      );
    }
    if (filteredTires && filteredTires.length) {
      if (
        Object.keys(shopsIndexed).length &&
        shopsIndexed[filteredTires[0].shopId]
      ) {
        setSelectedShopByIdDispatch(filteredTires[0].shopId);
      }
    }

    //

    // filteredTires
    let filteredTiresByService = [];

    if (inShopServiceSelected && !roadServiceSelected) {
      // filteredTires = filteredTires.filter(tire=>tire.inShopService)
      filteredTiresByService = filteredTires.filter(
        (tire) => tire.inShopService === true
      );
      if (!filteredTiresByService.length) {
        filteredTires = [];
      }
    }

    if (!inShopServiceSelected && roadServiceSelected) {
      // filteredTires = filteredTires.filter(tire=>tire.roadService)
      filteredTiresByService = filteredTires.filter(
        (tire) => tire.roadService === true
      );
      if (!filteredTiresByService.length) {
        filteredTires = [];
      }
    }

    if (inShopServiceSelected && roadServiceSelected) {
      // filteredTires = filteredTires.filter(if (tire.roadService && tire.inShopService) { return tire; })
      filteredTiresByService = filteredTires.filter(
        // (tire) => tire.roadService === true
        (tire) => {
          if (tire.roadService && tire.inShopService) {
            return tire;
          }
        }
      );
      if (!filteredTiresByService.length) {
        filteredTires = [];
      }
    }

    if (filteredTiresByService.length) filteredTires = filteredTiresByService;

    let filteredByAfterHours = [];

    if (open247Selected && open247MobileSelected) {
      filteredByAfterHours = filteredTires.filter((t) => {
        if (t.open247Mobile && t.open247) {
          return t;
        }
      });
    } else if (open247Selected && !open247MobileSelected) {
      filteredByAfterHours = filteredTires.filter((t) => t.open247 === true);
      if (!filteredByAfterHours.length) {
        filteredTires = [];
      }
    } else if (!open247Selected && open247MobileSelected) {
      filteredByAfterHours = filteredTires.filter(
        (t) => t.open247Mobile === true
      );
      if (!filteredByAfterHours.length) {
        filteredTires = [];
      }
    }

    // if (open247Selected && !open247MobileSelected) {
    //   filteredByAfterHours = filteredTires.filter((t) => t.open247 === true);
    // }

    // if (!open247Selected && open247MobileSelected) {
    //   filteredByAfterHours = filteredTires.filter(
    //     (t) => t.open247Mobile === true
    //   );
    // }

    if (filteredByAfterHours.length) filteredTires = filteredByAfterHours;
    if (
      open247Selected &&
      open247MobileSelected &&
      !filteredByAfterHours.length
    ) {
      filteredTires = [];
    }

    let filteredShopsIDS = {};
    setFilteredTiresDispatch(filteredTires);
    if (filteredTires.length) {
      filteredTires.forEach((tire) => {
        if (!filteredShopsIDS[tire.shopId]) {
          filteredShopsIDS[tire.shopId] = tire.shopId;
        }
      });
      setFilteredShopsIDSDispatch(Object.keys(filteredShopsIDS));
    } else {
      setFilteredShopsIDSDispatch([]);
    }
  };

  // useEffect(() => {
  //   if (selectedShop.length) {
  //     refs[selectedShop[0].PARK_ID].current.scrollIntoView({
  //       behavior: "smooth",
  //       block: "start"
  //     });
  //   }
  // }, [selectedShop]);

  let style = {};
  if (selectedLayout === EnumLayouts.MAP) style = { display: 'none' };

  // useEffect(() => {
  //   filterTires();
  // }, []);

  // useEffect(() => {
  //   filterTires();
  // }, [lat, lng]);

  filterTires();

  return (
    <div className='sidebar-tires' style={style}>
      <FiltersType />
      <div className='sidebar-tires-list' data-testid='tires-list-component'>
        {isDataLoading && (
          <div className='loading'>
            <p className='loading__p'>Loading</p>
            <div className='loading__dots'>
              <div className='loading__dot'></div>
              <div className='loading__dot'></div>
              <div className='loading__dot'></div>
              <div className='loading__dot'></div>
              <div className='loading__dot'></div>
            </div>
          </div>
        )}
        {!filteredTires.length && !isDataLoading && (
          <div className='no-results-div'>
            <p className='no-results-div-p'>Coming soon in this area ... </p>
          </div>
        )}

        {filteredTires.length && !isDataLoading
          ? filteredTires.map(
              (tire): JSX.Element => {
                return (
                  <TireCardTires
                    key={tire.id}
                    id={tire.id}
                    shopId={tire.shopId}
                    brand={tire.brand}
                    model={tire.model}
                    beforeDiscountPrice={tire.beforeDiscountPrice}
                    netPrice={tire.netPrice}
                    finalOutTheDoorPrice={tire.finalOutTheDoorPrice}
                    disposal={tire.disposal}
                    mounting={tire.mounting}
                    imgLink={tire.imgLink}
                    type={tire.type}
                    displayShortName={true}
                    displayDistance={true}
                    roadService={tire.roadService}
                    inShopService={tire.inShopService}
                    mobileFee={tire.mobileFee}
                    mobilePerHour={tire.mobilePerHour}
                    mobilePerMile={tire.mobilePerMile}
                    mobileHourMin={tire.mobileHourMin}
                    size={tire.size}
                  />
                );
              }
            )
          : null}
      </div>
    </div>
  );
}

const mapState = (state: IState): IListTiresMapToStateProps => {
  return {
    lat: state.center.lat,
    lng: state.center.lng,
    center: state.center,
    shopsIndexed: state.shopsIndexed,
    tires: state.tires,
    tiresIndexed: state.tiresIndexed,
    tiresByType: state.tiresByType,
    allIsChecked: state.filtersType.all.isChecked,
    steerIsChecked: state.filtersType.steer.isChecked,
    driveIsChecked: state.filtersType.drive.isChecked,
    trailerIsChecked: state.filtersType.trailer.isChecked,
    sortBy: state.sortBy,
    tiresByBrand: state.tiresByBrand,
    filtersBrands: state.filtersBrands,
    filtersSize: state.filtersSize,
    isDataLoading: state.isDataLoading,
    roadServiceSelected: state.roadServiceSelected,
    inShopServiceSelected: state.inShopServiceSelected,
    open247Selected: state.open247Selected,
    open247MobileSelected: state.open247MobileSelected,
    selectedLayout: state.selectedLayout,
    tiresBySize: state.tiresBySize,
  };
};

const MapDispatch = (dispatch) => {
  return {
    setSelectedShopByIdDispatch(selectedShop) {
      dispatch(setSelectedShopById(selectedShop));
    },
    setFilteredTiresDispatch(filteredTires) {
      dispatch(setFilteredTires(filteredTires));
    },
    setFilteredShopsIDSDispatch(shopsIds) {
      dispatch(setFilteredShopsIDS(shopsIds));
    },
    // setDataLoadingDispatch(condition) {
    //   dispatch(setDataLoading(condition));
    // },
  };
};

export default connect(mapState, MapDispatch)(ListTiresMap);
