const CHANGE_IN_SHOP_SERVICE_SELECTED = 'CHANGE_IN_SHOP_SERVICE_SELECTED';

interface IChangeInShopServiceSelected {
  type: string;
}

export const changeInShopServiceSelected = (): IChangeInShopServiceSelected => ({
  type: CHANGE_IN_SHOP_SERVICE_SELECTED
});

export default (state: boolean = false, action: IChangeInShopServiceSelected) => {
  switch (action.type) {
    case CHANGE_IN_SHOP_SERVICE_SELECTED:
      return !state;
    default:
      return state
  }
}