const SELECTED_DROPDOWN = "SELECTED_DROPDOWN";

export enum EnumDropdowns {
  NONE = "NONE",
  MOBILITY = 'MOBILITY',
  SORT = 'SORT',
  BRANDS = 'BRANDS',
  SIZE = 'SIZE',
  OPEN_247 = 'OPEN_247'
}

interface ISetSelectedDropDownAction {
  type: string,
  payload: string
}

export const setSelectedDropdown = (dropdownGroup: string): ISetSelectedDropDownAction => (
  {
    type: SELECTED_DROPDOWN,
    payload: dropdownGroup
  }
);

export default (state: string = EnumDropdowns.NONE, action: ISetSelectedDropDownAction) => {
  switch (action.type) {
    case SELECTED_DROPDOWN:
      return action.payload;
    default:
      return state;
  }
};