import React, { useState } from 'react';
import { connect } from 'react-redux';
import {
  IState,
  changeFilterCheck,
  EnumDropdowns,
  setSelectedDropdown,
} from '../../../store';

interface IFBMapToStateProps {
  [key: string]: boolean | string;
}

interface IFBMapDispatchProps {
  dispatchFilterChange: (filterKey: string) => void;
  setSelectedDropdownDispatch: (filterKey: string) => void;
}

// interface A {
//   a: number;
//   b: string;
//   [key: string]: number | string;
// }

// type B = A & {
//   c: Function;
// }

type IFiltersBrandsProps = IFBMapDispatchProps & IFBMapToStateProps;

function FilterBrands(props: IFiltersBrandsProps): JSX.Element {
  // const [dropSelected, setDropSelected] = useState<boolean>(false);
  const {
    filtersBrands,
    dispatchFilterChange,
    selectedDropdown,
    setSelectedDropdownDispatch,
  } = props;
  let btnCSS = 'filters-brandsDropBtn dropdown__btn';
  let dropCSS = 'invis';
  // if (dropSelected) {
  if (selectedDropdown === EnumDropdowns.BRANDS) {
    btnCSS = 'filters-brandsDropBtn dropdown__btn dropdown__btn-active';
    dropCSS = 'dropdown__content';
  }

  const handleClick = () => {
    if (selectedDropdown === EnumDropdowns.BRANDS) {
      setSelectedDropdownDispatch(EnumDropdowns.NONE);
    } else {
      setSelectedDropdownDispatch(EnumDropdowns.BRANDS);
    }
  };
  return (
    <div className='filters-brandsDropdown'>
      {/* <button className={btnCSS} onClick={() => setDropSelected(!dropSelected)}> */}
      <button className={btnCSS} onClick={() => handleClick()}>
        Brands&nbsp;
      </button>
      <div data-testid='filters-brands-dropdown' className={dropCSS}>
        {Object.keys(filtersBrands).map((filterKey, id) => {
          return (
            <label className='checkbox__label' key={id}>
              <input
                type='checkbox'
                className='checkbox__input'
                value={filtersBrands[filterKey].value}
                checked={filtersBrands[filterKey].isChecked}
                onChange={() => dispatchFilterChange(filterKey)}
                // data-testid={`brand-filter-btn-${filtersBrands[filterKey].value}`}
                data-testid='brand-filter-btn'
              />
              <span className='checkbox__custom'></span>
              {filterKey}&nbsp;
            </label>
          );
        })}
      </div>
    </div>
  );
}

const mapState = (state: IState): IFBMapToStateProps => {
  const keys = Object.keys(state.filtersBrands);
  const obj = { filtersBrands: state.filtersBrands };
  keys.forEach((key) => {
    obj[key] = state.filtersBrands[key].isChecked;
  });
  obj.selectedDropdown = state.selectedDropdown;
  return obj;
};

const mapDispatch = (dispatch): IFBMapDispatchProps => {
  return {
    dispatchFilterChange(filterKey) {
      dispatch(changeFilterCheck(filterKey));
    },
    setSelectedDropdownDispatch(sd: string) {
      dispatch(setSelectedDropdown(sd));
    },
  };
};

export default connect(mapState, mapDispatch)(FilterBrands);
