const SET_SHOPS_INDEXED = "SET_SHOPS_INDEXED";

import { IShop } from './shops';
import { IState } from './';

export interface IShopsIndexed {
  [key: string]: IShop;
}

interface IShopsIndexedAction {
  type: string;
  payload: IShopsIndexed;
}

export const setShopsIndexed = (shopsObject: IShopsIndexed): IShopsIndexedAction => ({
  type: SET_SHOPS_INDEXED,
  payload: shopsObject,
});

export const indexShops = () => (dispatch, getState: () => IState): void => {
  const state = getState();
  const shopObj = {};
  // O(n)
  state.shops.forEach((shop) => {
    shopObj[shop.id] = shop;
  });
  dispatch(setShopsIndexed(shopObj));
};

export default (state: IShopsIndexed = {}, action: IShopsIndexedAction) => {
  switch (action.type) {
    case SET_SHOPS_INDEXED:
      return action.payload;
    default:
      return state;
  }
};
