import React, { useState } from 'react';
import { connect } from 'react-redux';
import { Redirect, useLocation } from 'react-router-dom';
import PlacesAutocomplete, {
  geocodeByAddress,
  getLatLng,
} from 'react-places-autocomplete';
import slugify from 'slugify';

import { IState, ICenter, setCenter, setDataLoading } from '../../store';

const AutocompleteItem = ({ formattedSuggestion }): JSX.Element => (
  // <div className="Demo__suggestion-item">
  <div className='autocomplete-suggestion'>
    <i className='fa fa-map-marker Demo__suggestion-icon' />
    <strong>{formattedSuggestion.mainText}</strong>{' '}
    <small className='text-muted'>{formattedSuggestion.secondaryText}</small>
  </div>
);

const renderGeocodeSuccess = (lat: number, lng: number): JSX.Element => {
  return (
    <div className='alert alert-success' role='alert'>
      <strong>Success!</strong> Geocoder found latitude and longitude:{' '}
      <strong>
        {lat}, {lng}
      </strong>
    </div>
  );
};

const renderGeocodeFailure = (err: string): JSX.Element => {
  return (
    <div className='alert alert-danger' role='alert'>
      <strong>Error!</strong> {err}
    </div>
  );
};

function SearchForm(props): JSX.Element {
  const [address, setAddress] = useState<string>('');
  const [geocodeResults, setGeocodeResults] = useState<any>(null);
  const [loadingLocal, setLoadingLocal] = useState<boolean>(false);
  const [fireRedirect, setFireRedirect] = useState<boolean>(false);
  const [lat, setLat] = useState<number>(0);
  const [lng, setLng] = useState<number>(0);
  const [city, setCity] = useState<string>('');

  let { isLoading, setCenterDispatch, setDataLoadingDispatch, theme } = props;

  let CSSInputClass = 'autocomplete__input';
  let CSSDropdownClass =
    'autocomplete__dropdown-container Demo__autocomplete-container';
  if (theme && theme === 'dark') {
    CSSInputClass = 'autocomplete__input autocomplete__input-dark';
    CSSDropdownClass =
      'autocomplete__dropdown-container-dark Demo__autocomplete-container';
  }

  let PATH = false;
  const location = useLocation();
  let URL = location.pathname;
  if (URL === '/tires') PATH = false;
  if (URL === '/') PATH = true;

  const handleChange = (address: string): void => setAddress(address);

  const handleSelect = (
    address: string,
    placeId: string,
    suggestion: any
  ): void => {
    // console.log('Suggestion ', suggestion);
    const cityNameElements = address.split(', ');
    let cityName = '';
    if (cityNameElements.length > 1) {
      let len = cityNameElements.length;
      cityName = cityNameElements[len - 3] + ', ' + cityNameElements[len - 2];
    }

    setAddress(address);
    setLoadingLocal(true);
    setDataLoadingDispatch(true);
    geocodeByAddress(address)
      .then((results) => getLatLng(results[0]))
      .then(({ lat, lng }) => {
        setGeocodeResults(renderGeocodeSuccess(lat, lng));
        setLat(lat);
        setLng(lng);
        setCity(slugify(cityName));
        setLoadingLocal(false);
        setDataLoadingDispatch(false);
        const location = {
          address,
          lat,
          lng,
          cityName,
        };
        setCenterDispatch(location);
        setFireRedirect(true);
      })
      .catch((error) => {
        //console.log('Oh no!', error)
        setGeocodeResults(renderGeocodeFailure(error));
        setLoadingLocal(false);
      });
  };

  interface IInputProps {
    type: string;

    onBlur: () => void;
    onFocus: () => void;
    autoFocus: boolean;
    placeholder: string;
    name: string;
    id: string;
  }

  const inputProps: IInputProps = {
    type: 'text',
    onBlur: () => {
      //console.log('Blur event!');
    },
    onFocus: () => {
      //console.log('Focused!');
    },
    autoFocus: true,
    placeholder: 'My Location/ZIP/City',
    name: 'Demo__input',
    id: 'my-input-id',
  };

  const onFormSubmit = (e) => {
    setLoadingLocal(true);
    setDataLoading(true);
    e.preventDefault();
    // geocodeByAddress(address).then(({ lat, lng }) => {
    //   setGeocodeResults(renderGeocodeSuccess(lat, lng));
    //   setLat(lat);
    //   setLng(lng);
    //   // setLoadingLocal(false);
    //   // setDataLoading(false);

    //   const location = {
    //     address,
    //     lat,
    //     lng,
    //   };

    // }); // first .then()

    // console.log('FORM SUBMIT', address, lat, lng);

    // FETCH TIRES
    setFireRedirect(true);
  }; // onFormSubmit

  // console.log('FIRE REDIRECT ', fireRedirect);

  return (
    <div className='search__boxes'>
      <div className='search__boxes-wrapper'>
        <div className='search__places'>
          <PlacesAutocomplete
            onSelect={handleSelect}
            autocompleteItem={AutocompleteItem}
            value={address}
            onChange={handleChange}
          >
            {({
              getInputProps,
              suggestions,
              getSuggestionItemProps,
              loading,
            }) => (
              <div className='autocomplete' data-testid='component-search-from'>
                <input
                  {...getInputProps(inputProps)}
                  // className='autocomplete__input'
                  className={CSSInputClass}
                />
                <div className={CSSDropdownClass}>
                  {/* {loading && <div>Loading...</div>} */}
                  {suggestions.map((suggestion) => {
                    const className = suggestion.active
                      ? 'autocomplete__suggestion suggestion-item--active'
                      : 'autocomplete__suggestion suggestion-item';
                    // inline style for demonstration purpose
                    let style = suggestion.active
                      ? { backgroundColor: '#fafafa', cursor: 'pointer' }
                      : { backgroundColor: '#ffffff', cursor: 'pointer' };
                    if (theme && theme === 'dark') {
                      style = { backgroundColor: '#1b1f24', cursor: 'pointer' };
                    }
                    return (
                      <div
                        {...getSuggestionItemProps(suggestion, {
                          className,
                          style,
                        })}
                      >
                        <i className='fa fa-map-marker Demo__suggestion-icon' />
                        <span>{suggestion.description}</span>
                      </div>
                    );
                  })}
                </div>
              </div>
            )}
          </PlacesAutocomplete>
        </div>
        {/* <div className='search__button-wrapper'>
          <button
            onClick={onFormSubmit}
            className='search__button'
            type='submit'
          >
            {!isLoading ? (
              <i className='fa fa-search'></i>
            ) : (
              <i className='fa fa-spinner fa-pulse fa-1x fa-fw' />
            )}
          </button>
        </div> */}
      </div>
      {fireRedirect && PATH && (
        <Redirect to={`/commercial-tires-near/${city}/${lat},${lng}`} />
      )}

      {/* <Redirect to={ '/repair' } /> <Redirect to={ `/repair/${selectedOption}/${address}/${category}` } />  */}
    </div>
  );
}

interface ImapStateSF {
  center: ICenter;
}

const mapState: (state: IState) => ImapStateSF = (state) => {
  return {
    center: state.center,
    isLoading: state.isDataLoading,
  };
};

interface ImapDispatchSF {
  setCenterDispatch: (location: ICenter) => void;
}

const mapDispatch: (dispatch: any) => ImapDispatchSF = (dispatch) => {
  return {
    setCenterDispatch(location) {
      dispatch(setCenter(location));
    },
    setDataLoadingDispatch(cond) {
      dispatch(setDataLoading(cond));
    },
  };
};

export default connect(mapState, mapDispatch)(SearchForm);
