import React, { useState, useEffect } from 'react';
import TireCardTiresHomePage from '../tiresMapView/TiresCardTiresHomePage';
import { ITireDenormalized } from '../../../store';
import axios from 'axios';
import { Link } from 'react-router-dom';
import slugify from 'slugify';
import { getDistance } from '../../../utils';

function FeaturedTires(props): JSX.Element {
  const [roadServiceSelected, setRoadServiceSelected] = useState<boolean>(
    false
  );
  const [tires, setTires] = useState([]);
  const [allLowest, setAllLowest] = useState(933);
  const [driveLowest, setDriveLowest] = useState(933);
  const [steerLowest, setSteerLowest] = useState(933);
  const [trailerLowest, setTrailerLowest] = useState(933);
  const {
    // tiresHomePage,
    cityName,
    coord,
    // allLowest,
    // steerLowest,
    // driveLowest,
    // trailerLowest,
  } = props;

  const API_ROOT =
    'https://mebqwvo3g1.execute-api.us-east-1.amazonaws.com/default/getTires';

  useEffect(() => {
    if (process.env.BROWSER) {
      axios
        // .get(`${API_ROOT}/34.0522342,-118.2436849`)
        .get(`${API_ROOT}/${coord}`)
        .then((res) => res.data)
        .then((tires) => {
          // const featuredCities = {};
          // tires.forEach((tire) => {
          //   if(featuredCities[tire.city]){
          //     featuredCities[tire.city] = [...featuredCities[tire.city], tire]
          //   } else {
          //     featuredCities[tire.city] = [tire];
          //   }
          // })
          // setFeaturedCities(featuredCities);
          const lat = Number(coord.split(',')[0]);
          const lng = Number(coord.split(',')[1]);
          tires.forEach((tire) => {
            const shopLat = Number(JSON.parse(tire.geoJson).coordinates[1]);
            const shopLng = Number(JSON.parse(tire.geoJson).coordinates[0]);
            const distance = getDistance(
              { lat, lng },
              { lat: shopLat, lng: shopLng }
            );
            tire.distance = distance;
            if (tire.type === 'All') {
              if (tire.netPrice !== 0 && tire.netPrice < allLowest) {
                setAllLowest(tire.netPrice);
              }
              if (
                tire.finalOutTheDoorPrice !== 0 &&
                tire.finalOutTheDoorPrice < allLowest
              ) {
                setAllLowest(tire.finalOutTheDoorPrice);
              }
            }
            if (tire.type === 'Drive') {
              if (tire.netPrice !== 0 && tire.netPrice < driveLowest) {
                setDriveLowest(tire.netPrice);
              }
              if (
                tire.finalOutTheDoorPrice !== 0 &&
                tire.finalOutTheDoorPrice < allLowest
              ) {
                setDriveLowest(tire.finalOutTheDoorPrice);
              }
            }
            if (tire.type === 'Steer') {
              if (tire.netPrice !== 0 && tire.netPrice < steerLowest) {
                setSteerLowest(tire.netPrice);
              }
              if (
                tire.finalOutTheDoorPrice !== 0 &&
                tire.finalOutTheDoorPrice < allLowest
              ) {
                setSteerLowest(tire.finalOutTheDoorPrice);
              }
            }
            if (tire.type === 'Trailer') {
              if (tire.netPrice !== 0 && tire.netPrice < trailerLowest) {
                setTrailerLowest(tire.netPrice);
              }
              if (
                tire.finalOutTheDoorPrice !== 0 &&
                tire.finalOutTheDoorPrice < allLowest
              ) {
                setTrailerLowest(tire.finalOutTheDoorPrice);
              }
            }
          });
          setTires(tires);
        })
        .catch((err) => console.error(err));
    }
  }, []);

  const roadServiceClick = (bool) => {
    setRoadServiceSelected(bool);
  };
  let InShopCSS =
    'home__deals-row-featured-service-p home__deals-row-featured-service-p-active';
  let roadServiceCSS = 'home__deals-row-featured-service-p';
  if (roadServiceSelected) {
    InShopCSS = 'home__deals-row-featured-service-p';
    roadServiceCSS =
      'home__deals-row-featured-service-p home__deals-row-featured-service-p-active';
  }
  return (
    <React.Fragment>
      {tires.length ? (
        <div className='home__deals-row'>
          <h2 className='home__deals-row-title'>
            Truck tires near <br className='home__deals-row-title-br' />
            {cityName}&nbsp;
            <Link
              to={`/commercial-tires-near/${slugify(cityName)}/${coord}`}
              className='home__deals-row-title-link'
            >
              view more&nbsp;
            </Link>
          </h2>
          <div className='home__deals-row-pricesByType'>
            {allLowest !== 933 ? (
              <p className='home__deals-row-allPrice posPrice-btn'>
                All Tires from{' '}
                <span className='home__deals-row-allPrice-tag'>
                  ${allLowest}&nbsp;
                </span>
              </p>
            ) : null}
            {steerLowest !== 933 ? (
              <p className='home__deals-row-steerPrice posPrice-btn'>
                Steer Tires from{' '}
                <span className='home__deals-row-steerPrice-tag'>
                  ${steerLowest}&nbsp;
                </span>
              </p>
            ) : null}

            {driveLowest !== 933 ? (
              <p className='home__deals-row-drivePrice posPrice-btn'>
                Drive Tires from{' '}
                <span className='home__deals-row-drivePrice-tag'>
                  ${driveLowest}&nbsp;
                </span>
              </p>
            ) : null}

            {trailerLowest !== 933 ? (
              <p className='home__deals-row-trailerPrice posPrice-btn'>
                Trailer Tires from{' '}
                <span className='home__deals-row-trailerPrice-tag'>
                  ${trailerLowest}&nbsp;
                </span>
              </p>
            ) : null}
          </div>
          <div className='home__deals-row-featured'>
            <h3 className='home__deals-row-featured-title'>
              Commercial tires&nbsp;
            </h3>
            {/* <div className='home__deals-row-featured-service'>
          <p className={InShopCSS} onClick={() => roadServiceClick(false)}>
            In Shop Service&nbsp;
          </p>
          <p className={roadServiceCSS} onClick={() => roadServiceClick(true)}>
            Road Service&nbsp;
          </p>
        </div> */}
          </div>
          <div className='home__deals-row-list'>
            {tires.map(
              (tire: ITireDenormalized, IDX: number): JSX.Element => {
                return (
                  <Link
                    to={`/commercial-tires-near/${slugify(cityName)}/${coord}`}
                  >
                    <TireCardTiresHomePage
                      key={tire.id}
                      id={tire.id}
                      shopId={tire.shopId}
                      brand={tire.brand}
                      model={tire.model}
                      beforeDiscountPrice={tire.beforeDiscountPrice}
                      netPrice={tire.netPrice}
                      finalOutTheDoorPrice={tire.finalOutTheDoorPrice}
                      mounting={tire.mounting}
                      imgLink={tire.imgLink}
                      type={tire.type}
                      shopShortName={tire.shopShortName}
                      inShopService={tire.inShopService}
                      distance={tire.distance}
                    />
                  </Link>
                );
              }
            )}
          </div>
        </div>
      ) : (
        <div className='loading home__deals-row-loading'>
          <p className='loading__p'>Loading&nbsp;</p>
          <div className='loading__dots'>
            <div className='loading__dot'></div>
            <div className='loading__dot'></div>
            <div className='loading__dot'></div>
            <div className='loading__dot'></div>
            <div className='loading__dot'></div>
          </div>
        </div>
      )}
    </React.Fragment>
  );
}

export default FeaturedTires;
