const CHANGE_OPEN_SEARCH = 'CHANGE_OPEN_SEARCH';

interface IChangeOpenSearch {
  type: string;
}

export const changeOpenSearch = (): IChangeOpenSearch => ({
  type: CHANGE_OPEN_SEARCH
});

export default (state: boolean = false, action: IChangeOpenSearch) => {
  switch (action.type) {
    case CHANGE_OPEN_SEARCH:
      return !state;
    default:
      return state
  }
}