const ADD_SIZE_FILTER = "ADD_SIZE_FILTER";
const ADD_SIZES_FILTERS = "ADD_SIZES_FILTERS";
const CHECK_SIZE_FILTER = "CHECK_SIZE_FILTER";

export interface IFilterSizeObject {
  value: number | string;
  isChecked: boolean;
  label: string;
}

export interface IFiltersSize {
  [key: string]: IFilterSizeObject;
}

interface IFIlterSizeAction {
  type: string;
  payload: IFilterSizeObject;
}

interface IFIltersSizesAction {
  type: string;
  payload: IFiltersSize;
}

interface IFIlterToChangeAction {
  type: string;
  payload: string | number;
}

export const addSizeFilter = (sizeFilter: IFilterSizeObject): IFIlterSizeAction => ({
  type: ADD_SIZE_FILTER,
  payload: sizeFilter
})

export const addSizesFilters = (sizesObj: IFiltersSize): IFIltersSizesAction => ({
  type: ADD_SIZES_FILTERS,
  payload: sizesObj
})

export const changeFilterSize = (filterToChange: string | number): IFIlterToChangeAction => ({
  type: CHECK_SIZE_FILTER,
  payload: filterToChange
})

