export const sizeStateObject: IFiltersSize = {
  "0": { isChecked: false, value: '0', label: "295/75R22.5" },
  "1": { isChecked: false, value: '1', label: "11R22.5" },
  "2": { isChecked: false, value: '2', label: "275/80R22.5" },
  "3": { isChecked: false, value: '3', label: "225/70R19.5" },
  "4": { isChecked: false, value: '4', label: "11R24.5" },
  "5": { isChecked: false, value: '5', label: "285/75R24.5" },
  "7": { isChecked: false, value: '7', label: "315/80R22.5" },
  "8": { isChecked: false, value: '8', label: "245/75R22.5" },
  "9": { isChecked: false, value: '9', label: "265/75R22.5" },
  "10": { isChecked: false, value: '10', label: "10R22.5" },
}

const initialState: IFiltersSize = {
  "0": { isChecked: false, value: 0, label: "295/75R22.5" },
}

// IFIltersSizesAction | IFIlterSizeAction
export default (state: IFiltersSize = {}, action: IFIltersSizesAction | IFIlterSizeAction | IFIlterToChangeAction) => {
  switch (action.type) {
    // case ADD_SIZE_FILTER:
    //   let newState: IFiltersSize = { ...state };
    //   let newSizeFilter = action.payload;
    //   newState[newSizeFilter.value.toString()] = newSizeFilter;
    //   return newState;
    case ADD_SIZES_FILTERS:
      return action.payload;
    case CHECK_SIZE_FILTER:
      let newState1 = { ...state };
      let size = action.payload;
      newState1[size.toString()].isChecked = !newState1[size.toString()].isChecked;
      return newState1;
    default:
      return state
  }
}