import React, { useState, useEffect } from 'react';
import {
  GoogleMap,
  withScriptjs,
  withGoogleMap,
  Marker,
  InfoWindow,
} from 'react-google-maps';
import { compose, withProps } from 'recompose';
import { connect } from 'react-redux';
import mapStyles from './mapStyles';
import {
  setSelectedShopById,
  setSelectedShop,
  sortByDistance,
  setCenter,
  changeIsModalOpen,
  setSelectedTireById,
  EnumLayouts,
} from '../../store';
import { parsePhoneNumber } from 'libphonenumber-js';
import SVGPhone from './svg/SVGPhone';
import SVGLink from './svg/SVGLink';

const Map = compose(
  withProps({
    loadingElement: <div style={{ height: '100%' }}></div>,
    containerElement: <div style={{ height: '100vh' }}></div>,
    mapElement: <div style={{ height: '100%' }}></div>,
    // googleMapURL: `https://maps.googleapis.com/maps/api/js?v=3.exp&libraries=geometry,drawing,places&key=${process.env.GOOGLE_MAP_KEY}`,
  }),
  // withScriptjs,
  withGoogleMap
)((props) => {
  let {
    shops,
    center,
    selectedShop,
    // mapView,
    // defaultCenter,
    setSelectedTireByIdDispatch,
    setSelectedShopByIdDispatch,
    changeIsModalOpenDispatch,
    tiresByShopId,
    selectedLayout,
    filteredShopsIDS,
  } = props;

  let markers = [];
  if (filteredShopsIDS.length) {
    filteredShopsIDS.forEach((id) => {
      markers.push(shops[id]);
    });
  }

  const [selectedMarker, setSelectedMarker] = useState(null);
  const [mapProps, setMapProps] = useState({
    defaultZoom: 9,
    defaultCenter: defaultCenter,
    defaultOptions: { styles: mapStyles },
  });

  const deliver = (m) => {
    setSelectedMarker(m);
    // props.sort("OLARE");
    setSelectedShopByIdDispatch(m.id);
    // props.setSelectedShopX(m)
  };

  // let defaultCenter = {
  //   lat: Number(center.lat) - 0.15,
  //   lng: Number(center.lng) - 0.5,
  // };
  // if (mapView)
  //   defaultCenter = { lat: Number(center.lat), lng: Number(center.lng) };

  // const mapProps = {
  //   defaultZoom: 8,
  //   defaultCenter: defaultCenter,
  //   defaultOptions: { styles: mapStyles },
  // };

  useEffect(() => {
    let newProps = mapProps;
    newProps.center = center;
    setMapProps(newProps);
  }, [center.lat, center.lng]);

  //   <GoogleMap
  //   defaultZoom={8}
  //   defaultCenter={defaultCenter}
  //   defaultOptions={{ styles: mapStyles }}
  //   center={center}
  // >

  const handleInfoClick = (shopId) => {
    setSelectedTireByIdDispatch(tiresByShopId[shopId][0]);
    setSelectedShopByIdDispatch(shopId);
    changeIsModalOpenDispatch();
  };

  let phoneParsed;
  let phoneInter;
  let phoneDigits;
  if (selectedMarker) {
    phoneParsed = parsePhoneNumber(selectedMarker.phone, 'US');
    phoneInter = phoneParsed.formatNational();
    phoneDigits = selectedMarker.phone.toString().replace(/\D/g, '');
  }

  let defaultCenter = {
    lat: Number(center.lat) - 0.15,
    lng: Number(center.lng) - 0.5,
  };

  let mapView = false;
  if (selectedLayout === EnumLayouts.MAP) mapView = true;
  if (!mapView) {
    center = {
      lat: Number(center.lat) - 0.15,
      lng: Number(center.lng) - 0.5,
    };
  }

  if (mapView) {
    defaultCenter = { lat: Number(center.lat), lng: Number(center.lng) };
    center = { lat: Number(center.lat), lng: Number(center.lng) };
  }

  return (
    <GoogleMap {...mapProps}>
      {markers.length &&
        markers.map((m) => {
          const lat = m.lat;
          const lng = m.lng;
          let markerColor = '/v10.png';
          let markerSize = 57;
          let zIndex = 1;
          if (selectedMarker) {
            if (m.id === selectedMarker.id) {
              markerColor = '/v12.png';
              // markerColor = "/v2.png";
              markerSize = 81;
              // changeMarkerToBlue();
              zIndex = 33;
            }
          }
          if (selectedShop.length) {
            if (m.id === selectedShop[0].id) {
              markerColor = '/v12.png';
              // markerColor = "/v2.png";
              markerSize = 75;
              // changeMarkerToBlue();
              zIndex = 33;
            }
          }
          return (
            <Marker
              key={m.id}
              position={{ lat, lng }}
              // onMouseOver={() => setSelectedMarker(m)}
              // onMouseOut={() => setSelectedMarker(null)}
              icon={{
                // url: SVG1
                // url: "https://img.icons8.com/cotton/64/000000/place-marker.png",
                // url: markerURL,
                url: markerColor,
                scaledSize: new window.google.maps.Size(markerSize, markerSize),
              }}
              onClick={() => {
                deliver(m);
                // setSelectedMarker(m);
              }}
              zIndex={zIndex}
            />
          );
        })}

      {selectedMarker && (
        <InfoWindow
          position={{
            // lat: selectedMarker.coordinates[1] + 0.012,
            lat: selectedMarker.lat,
            lng: selectedMarker.lng,
          }}
          onCloseClick={() => {
            setSelectedMarker(null);
          }}
        >
          <div className='info-window'>
            <h2
              className='info-window-title'
              onClick={() => handleInfoClick(selectedMarker.id)}
            >
              {selectedMarker.name}
            </h2>
            <p className='info-window-p'>{selectedMarker.address}</p>
            <p className='info-window-phone'>
              <SVGPhone />
              <a href={'tel:' + phoneDigits}>{phoneInter}</a>
            </p>
            <p
              className='info-window-more'
              onClick={() => handleInfoClick(selectedMarker.id)}
            >
              <SVGLink />
              More Info
            </p>
          </div>
        </InfoWindow>
      )}
    </GoogleMap>
  );
});

const mapState = (state) => {
  return {
    shops: state.shopsIndexed,
    // center: state.center,
    selectedShop: state.selectedShop,
    tiresByShopId: state.tiresByShopId,
    selectedLayout: state.selectedLayout,
    filteredShopsIDS: state.filteredShopsIDS,
  };
};

const mapDispatch = (dispatch) => {
  return {
    setSelectedTireByIdDispatch(id) {
      dispatch(setSelectedTireById(id));
    },
    setSelectedShopByIdDispatch(id) {
      dispatch(setSelectedShopById(id));
    },
    setSelectedShop(shop) {
      dispatch(setSelectedShop(shop));
    },
    sort() {
      dispatch(sortByDistance());
    },
    changeIsModalOpenDispatch() {
      dispatch(changeIsModalOpen());
    },
  };
};

export default connect(mapState, mapDispatch)(Map);
