const SET_TIRES_BY_SIZE = 'SET_TIRES_BY_SIZE';

export interface ITiresBySize {
  [key: string]: number[];
}

interface ITiresBySizeAction {
  type: string;
  payload: ITiresBySize;
}

export const setTiresBySize = (tiresBySize: ITiresBySize): ITiresBySizeAction => ({
  type: SET_TIRES_BY_SIZE,
  payload: tiresBySize
})

export default (state: ITiresBySize = {}, action: ITiresBySizeAction) => {
  switch (action.type) {
    case SET_TIRES_BY_SIZE:
      return action.payload;
    default:
      return state;
  }
}