const CHANGE_ROAD_SERVICE_SELECTED = 'CHANGE_ROAD_SERVICE_SELECTED';

interface IChangeRoadServiceSelected {
  type: string;
}

export const changeRoadServiceSelected = (): IChangeRoadServiceSelected => ({
  type: CHANGE_ROAD_SERVICE_SELECTED
});

export default (state: boolean = false, action: IChangeRoadServiceSelected) => {
  switch (action.type) {
    case CHANGE_ROAD_SERVICE_SELECTED:
      return !state;
    default:
      return state
  }
}