const SELECT_LAYOUT = "SELECT_LAYOUT";

export enum EnumLayouts {
  MAP = "MAP",
  LIST = 'LIST',
}

interface ISetSelectedLayoutAction {
  type: string,
  payload: string
}

export const setSelectedLayout = (layoutOption: string): ISetSelectedLayoutAction => (
  {
    type: SELECT_LAYOUT,
    payload: layoutOption
  }
);

export default (state: string = EnumLayouts.LIST, action: ISetSelectedLayoutAction) => {
  switch (action.type) {
    case SELECT_LAYOUT:
      return action.payload;
    default:
      return state;
  }
};