const SET_TIRES_BY_BRAND = 'SET_TIRES_BY_BRAND';

export interface ITiresByBrand {
  [key: string]: number[];
}

interface ITiresByBrandAction {
  type: string;
  payload: ITiresByBrand;
}

export const setTiresByBrand = (tiresByBrand: ITiresByBrand): ITiresByBrandAction => ({
  type: SET_TIRES_BY_BRAND,
  payload: tiresByBrand
})

export default (state: ITiresByBrand = {}, action: ITiresByBrandAction) => {
  switch (action.type) {
    case SET_TIRES_BY_BRAND:
      return action.payload;
    default:
      return state;
  }
}