import React, { useState } from 'react';
import { connect } from 'react-redux';
import {
  IState,
  IShop,
  ITireDenormalized,
  ITiresIndexed,
  ITiresByShopId,
} from '../../store';
import ShopInfoTireCard from './tiresMapView/ShopInfoTireCard';
import TiresCardTire from '../components/tiresMapView/TireCardTires';
import SVGPrice from './svg/SVGPrice';
import SVGLocation from './svg/SVGLocation';
import SVGPhone from './svg/SVGPhone';
import SVGMap from './svg/SVGMap';
import SVGLink from './svg/SVGLink';
import SVGClock from './svg/SVGClock';
import { parsePhoneNumber } from 'libphonenumber-js';

interface IShopPageMapState {
  selectedTire: ITireDenormalized[];
  selectedShop: IShop[];
  tiresIndexed: ITiresIndexed;
  tiresByShopId: ITiresByShopId;
}

interface IMapDispatchProps {}

interface ShopPageProps extends IShopPageMapState, IMapDispatchProps {}

function ShopPage(props: ShopPageProps) {
  const [roadServiceSelected, setRoadServiceSelected] = useState<boolean>(
    false
  );
  const { selectedTire, selectedShop, tiresIndexed, tiresByShopId } = props;
  const {
    id,
    name,
    address,
    lat,
    lng,
    distance,
    roadService,
    phone,
    inShopService,
    phoneAfterHours,
    mounting,
    mountingAfterHours,
    mobileFee,
    mobileFeeAfterHours,
    mobilePerHour,
    mobilePerHourAfterHours,
    mobilePerMile,
    mobilePerMileAfterHours,
    open247,
    open247Mobile,
    websiteName,
    hours,
    mobileHourMin,
    mobileHourMinAfterHours,
    balancing,
    disposal,
  } = selectedShop[0];
  const afterHours = open247 || open247Mobile;
  // https://www.google.com/maps/dir/?api=1&parameters
  // https://www.google.com/maps/search/?api=1&query=47.5951518,-122.3316393

  // const googleMapLink = `https://www.google.com/maps/search/?api=1&query=${lat},${lng}`;
  const googleMapLink = `https://www.google.com/maps/search/?api=1&query=${encodeURIComponent(
    address
  )}`;
  const phoneParsed = parsePhoneNumber(phone, 'US');
  const phoneInter = phoneParsed.formatNational();
  let phoneParsedAfter;
  let phoneInterAfter;
  const phoneDigits = phone.toString().replace(/\D/g, '');
  let phoneDigitsAfter;
  if (phoneAfterHours) {
    phoneDigitsAfter = phoneAfterHours.toString().replace(/\D/g, '');
    phoneParsedAfter = parsePhoneNumber(phoneAfterHours, 'US');
    phoneInterAfter = phoneParsedAfter.formatNational();
  }
  const shopTiresIDS = tiresByShopId[id];
  const roadServiceClick = (bool) => {
    setRoadServiceSelected(bool);
  };
  let InShopCSS =
    'home__deals-row-featured-service-p home__deals-row-featured-service-p-active';
  let roadServiceCSS = 'home__deals-row-featured-service-p';
  if (roadServiceSelected) {
    InShopCSS = 'home__deals-row-featured-service-p';
    roadServiceCSS =
      'home__deals-row-featured-service-p home__deals-row-featured-service-p-active';
  }
  let roadServiceShopRate;
  if (roadService) {
    roadServiceShopRate = '';
    if (mobileFee) {
      roadServiceShopRate += `$${mobileFee} + `;
    }
    if (mobileHourMin) {
      roadServiceShopRate += `${mobileHourMin} hour min `;
    }
    if (mobilePerHour) {
      roadServiceShopRate += `$${mobilePerHour} per hour `;
    }
    if (mobilePerMile) {
      roadServiceShopRate += `$${mobilePerMile} per mile`;
    }
  }
  let roadServiceAfterHoursShopRate;
  if (open247Mobile) {
    roadServiceAfterHoursShopRate = '';
    if (mobileFeeAfterHours) {
      roadServiceAfterHoursShopRate += `$${mobileFeeAfterHours} + `;
    }
    if (mobileHourMinAfterHours) {
      roadServiceAfterHoursShopRate += `${mobileHourMinAfterHours} hour min `;
    }
    if (mobilePerHourAfterHours) {
      roadServiceAfterHoursShopRate += `$${mobilePerHourAfterHours} per hour `;
    }
    if (mobilePerMileAfterHours) {
      roadServiceAfterHoursShopRate += `$${mobilePerMileAfterHours} per mile`;
    }
  }
  let mobility;
  let onlyOneMobility;
  if (roadService && !inShopService) {
    onlyOneMobility = true;
    mobility = 'Road Service Only';
  } else if (!roadService && inShopService) {
    onlyOneMobility = true;
    mobility = 'In Shop Service';
  } else {
    onlyOneMobility = false;
  }
  return (
    <div className='shopInfoModal' data-testid='shopModal-component'>
      <div
        className='shopInfoModal-selectedTire'
        data-testid='shopModal-component-selected-tire'
      >
        <TiresCardTire
          // data-testid tires-card-component-shopModal
          id={selectedTire[0].id}
          shopId={selectedTire[0].shopId}
          brand={selectedTire[0].brand}
          model={selectedTire[0].model}
          beforeDiscountPrice={selectedTire[0].beforeDiscountPrice}
          netPrice={selectedTire[0].netPrice}
          finalOutTheDoorPrice={selectedTire[0].finalOutTheDoorPrice}
          mounting={selectedTire[0].mounting}
          imgLink={selectedTire[0].imgLink}
          type={selectedTire[0].type}
          displayShortName={false}
          displayDistance={false}
          roadService={roadService}
          inShopService={inShopService}
          mobileFee={mobileFee}
          mobilePerHour={mobilePerHour}
          mobilePerMile={mobilePerMile}
          mobileHourMin={mobileHourMin}
          testParent={'shopModal'}
          size={selectedTire[0].size}
        />
      </div>
      <div className='shopInfoModal-info'>
        <div className='shopInfoModal-info__title'>
          <h3
            className='shopInfoModal-info__title-inner'
            data-testid='shopModal-component-title'
          >
            {name}
          </h3>
          {/* <span className='shopInfoModal-info__title-distance'>
            {distance} mi away
          </span> */}
          <p className='shopInfoModal-info__title-distance-p'>
            {distance} mi away
          </p>
        </div>
        <div className='shopInfoModal__content'>
          {onlyOneMobility && (
            <div className='shopInfoModal-info__oneMobility'>
              <h3 className='shopInfoModal-info__oneMobility-title'>
                {mobility}
              </h3>
            </div>
          )}
          <p className='shopInfoModal-info__address'>
            <SVGLocation />
            {address}
          </p>
          <p className='shopInfoModal-info__google'>
            <a href={googleMapLink} target='_blank'>
              <SVGMap />
              Directions
            </a>
          </p>
          <p className='shopInfoModal-info__phone'>
            <SVGPhone />
            <a href={'tel:' + phoneDigits}>{phoneInter}</a>
          </p>
          {websiteName && (
            <p className='shopInfoModal-info__websiteLink'>
              <SVGLink />
              <a href={`http://${websiteName}`} target='_blank'>
                {websiteName}
              </a>
            </p>
          )}
          <div className='shopInfoModal-info-247'>
            {open247 && (
              <div className='shopInfoModal-info__247-InShop btn247'>
                <SVGClock />
                24/7 In Shop Service
              </div>
            )}
            {open247Mobile && (
              <div className='shopInfoModal-info__247-roadService btn247'>
                <SVGClock />
                24/7 Road Service
              </div>
            )}
          </div>
          {inShopService && (
            <React.Fragment>
              <div className='shopInfoModal-info-shopRates-title'>
                <h3 className='shopInfoModal-info-shopRates-title-inner'>
                  Shop Rates
                </h3>
              </div>
              <p className='shopInfoModal-info__inShopRate'>
                <SVGPrice />
                Mounting: {mounting ? `$${mounting}` : 'included in price'}
              </p>
              {balancing !== 0 && (
                <p className='shopInfoModal-info__inShopRate'>
                  <SVGPrice />
                  Balancing: ${balancing}
                  {/* {finalOutTheDoorPrice && 'Disposal: Included in Price'} */}
                </p>
              )}
              {disposal !== 0 && (
                <p className='shopInfoModal-info__inShopRate'>
                  <SVGPrice />
                  Disposal: ${disposal}
                  {/* {finalOutTheDoorPrice && 'Disposal: Included in Price'} */}
                </p>
              )}
            </React.Fragment>
          )}
          {roadService && (
            <React.Fragment>
              <div className='shopInfoModal-info-roadServiceRates-title'>
                <h3 className='shopInfoModal-info-roadServiceRates-title-inner'>
                  Road Service Rates
                </h3>
              </div>
              <div className='shopInfoModal-info-roadServiceRates'>
                <p className='shopInfoModal-info__roadServiceRate'>
                  <SVGPrice />
                  Road Service:{' '}
                  {roadServiceShopRate
                    ? roadServiceShopRate
                    : 'rates not specified, please call'}
                </p>
              </div>
            </React.Fragment>
          )}
          {afterHours && (
            <React.Fragment>
              <div className='shopInfoModal-info-afterHoursRates-title'>
                <h3 className='shopInfoModal-info-afterHoursRates-title-inner'>
                  After Hours Rates
                </h3>
              </div>
              <div className='shopInfoModal-info-afterHoursRates-info'>
                {phoneAfterHours && (
                  <p className='shopInfoModal-info__phoneAfterHours'>
                    <SVGPhone />
                    After Hours:{' '}
                    <a href={'tel:' + phoneDigitsAfter}>{phoneInterAfter}</a>
                  </p>
                )}
                {open247 && (
                  <p className='shopInfoModal-info-afterHoursRates-info-inShop'>
                    <SVGPrice />
                    {mountingAfterHours
                      ? `In Shop Installment: $${mountingAfterHours}`
                      : 'After hours rates apply, please call for details'}
                  </p>
                )}
                {open247Mobile && (
                  <div className='shopInfoModal-info-afterHours-roadService'>
                    {mobileFeeAfterHours !== 0 || mobileHourMin !== 0 ? (
                      <p className='shopInfoModal-info-afterHours-roadServiceRate'>
                        <SVGPrice />
                        Road Service:{' '}
                        {roadServiceAfterHoursShopRate
                          ? roadServiceAfterHoursShopRate
                          : 'rates not specified, please call'}
                      </p>
                    ) : (
                      <p>
                        <SVGPrice />
                        Road Service: rates not specified, please call
                      </p>
                    )}
                  </div>
                )}
              </div>
            </React.Fragment>
          )}
        </div>
      </div>
      <div className='shopInfoModal-tires'>
        <div className='shopInfoModal-tires__title'>
          <h4 className='shopInfoModal-tires__title-inner'>
            All Tires From {name}
          </h4>
          {/* <div className='home__deals-row-featured-service'>
            <p className={InShopCSS} onClick={() => roadServiceClick(false)}>
              In Shop Service
            </p>
            <p
              className={roadServiceCSS}
              onClick={() => roadServiceClick(true)}
            >
              Road Service
            </p>
          </div> */}
        </div>
        <div className='shopInfoModal-tires__list'>
          {shopTiresIDS.map(
            (tireID: number): JSX.Element => {
              const tire = tiresIndexed[tireID];
              // data-testid shopInfo-tireCard-shopModal
              return (
                <ShopInfoTireCard
                  key={tire.id}
                  id={tire.id}
                  shopId={tire.shopId}
                  brand={tire.brand}
                  model={tire.model}
                  beforeDiscountPrice={tire.beforeDiscountPrice}
                  netPrice={tire.netPrice}
                  finalOutTheDoorPrice={tire.finalOutTheDoorPrice}
                  mounting={tire.mounting}
                  disposal={tire.disposal}
                  imgLink={tire.imgLink}
                  type={tire.type}
                  distance={distance}
                  displayDistance={false}
                  displayFinalPrice={true}
                  testParent={'shopModal'}
                />
              );
            }
          )}
        </div>
      </div>
    </div>
  );
}

const mapStateToProps = (state: IState): IShopPageMapState => {
  return {
    selectedTire: state.selectedTire,
    selectedShop: state.selectedShop,
    tiresByShopId: state.tiresByShopId,
    tiresIndexed: state.tiresIndexed,
  };
};

const MapDispatch = () => {
  return {};
};

export default connect(mapStateToProps, MapDispatch)(ShopPage);
