const SET_TIRES = "SET_TIRES";
import axios from 'axios';
import { getDistance } from '../../utils';
import {
  setCenter,
  setShops,
  setShopsIndexed,
  setTiresIndexed,
  setTiresByShopId,
  addBrandsFilters,
  setTiresByType,
  setTiresByBrand,
  changeIsModalOpen,
  // setSelectedShop,
  setDataLoading,
  addSizesFilters,
  sizeStateObject,
  IShop
} from '../';
import { batch } from 'react-redux'

export interface ITire {
  hashKey?: number;
  geoJson?: string;
  rangeKey?: string;
  geohash: number;
  id: number | string;
  createdAt: string;
  shopId: number | string;
  condition: string | number;
  brand: string;
  model: string;
  type: string;
  size: string | number;
  imgLink: string;
  beforeDiscountPrice: number;
  netPrice: number;
  finalOutTheDoorPrice: number;
  finalPriceIncludesBalance: boolean;
  drivePositionTireShoulder?: string;
  allPositionSecondType?: string;
  discountType?: string;
  discountAmount?: number;
}

export interface ITireDenormalized extends ITire {
  shopName: string;
  shopShortName: string;
  address: string;
  phone: string;
  phoneAfterHours?: string,
  roadService: boolean;
  inShopService: boolean,
  open247: boolean,
  open247Mobile: boolean,
  mounting: number;
  mountingAfterHours: number,
  balancing: number;
  disposal: number;
  mobileFee: number,
  mobileFeeAfterHours: number,
  mobilePerHour: number,
  mobilePerHourAfterHours: number,
  mobilePerMile: number,
  mobilePerMileAfterHours: number,
  websiteName?: string,
  hours: number,
  mobileHourMin: number,
  mobileHourMinAfterHours: number,
  valveStem: number;
  serviceInfo: string;
  ply: number;
  fetTax: number;
  orderStatus: string;
  loadRange: string;
  loadIndex: string;
  distance?: number;
}

interface ITireAction {
  type: string;
  payload: ITireDenormalized[];
}

export const setTires = (tires: ITireDenormalized[]): ITireAction => ({
  type: SET_TIRES,
  payload: tires
})

