export const fetchTires = (lat, lng) => (dispatch, getState) => {
  axios
    .get(
      `https://mebqwvo3g1.execute-api.us-east-1.amazonaws.com/default/getTires/${lat},${lng}`
    )
    .then((res) => res.data)
    .then((tires) => {
      // shopId -> shop
      const shops = {};
      // shopId -> [tiresIds]
      const shopsObj = {};
      // tireId -> tire;
      const tiresObj = {};
      const typeObj = {
        All: [],
        Trailer: [],
        Drive: [],
        Steer: [],
      };
      let shopsArr = [];
      const brandsObj = {};
      const sizesObj = {};
      const brandObjWithIds = {};
      tires.forEach((tire) => {
        tiresObj[tire.id] = tire;
        if (!shops[tire.shopId]) {
          // const shop = {
          //   id: 0,
          //   name: '',
          //   shortName: '',
          //   address: '',
          //   lat: 0,
          //   lng: 0,
          //   distance: 0,
          // };
          const shop = {} as IShop;
          const shopLat = Number(JSON.parse(tire.geoJson).coordinates[1]);
          const shopLng = Number(JSON.parse(tire.geoJson).coordinates[0]);
          const distance = getDistance(
            { lat, lng },
            { lat: shopLat, lng: shopLng }
          );
          shop.id = tire.shopId;
          shop.name = tire.shopName;
          shop.shortName = tire.shopShortName;
          shop.address = tire.address;
          shop.phone = tire.phone;
          shop.phoneAfterHours = tire.phoneAfterHours;
          shop.lat = shopLat;
          shop.lng = shopLng;
          shop.roadService = tire.roadService;
          shop.inShopService = tire.inShopService;
          shop.open247 = tire.open247;
          shop.open247Mobile = tire.open247Mobile;
          shop.mounting = tire.mounting;
          shop.mountingAfterHours = tire.mountingAfterHours;
          shop.balancing = tire.balancing;
          shop.disposal = tire.disposal;
          shop.mobileFee = tire.mobileFee;
          shop.mobileFeeAfterHours = tire.mobileFeeAfterHours;
          shop.mobilePerHour = tire.mobilePerHour;
          shop.mobilePerHourAfterHours = tire.mobilePerHourAfterHours;
          shop.mobilePerMile = tire.mobilePerMile;
          shop.mobilePerMileAfterHours = tire.mobilePerMileAfterHours;
          shop.websiteName = tire.websiteName;
          shop.hours = tire.hours;
          shop.mobileHourMin = tire.mobileHourMin;
          shop.mobileHourMinAfterHours = tire.mobileHourMinAfterHours;
          shop.valveStem = tire.valveStem;
          shop.serviceInfo = tire.serviceInfo;
          shop.distance = distance;
          shops[tire.shopId] = shop;
          shopsArr = [...shopsArr, shop];
        }
        if (shopsObj[tire.shopId]) {
          shopsObj[tire.shopId] = shopsObj[tire.shopId].concat([tire.id]);
        } else {
          shopsObj[tire.shopId] = [tire.id];
        }
        if (typeObj[tire.type]) {
          typeObj[tire.type] = typeObj[tire.type].concat([tire.id]);
        } else {
          typeObj[tire.type] = [tire.id];
        }
        if (brandObjWithIds[tire.brand]) {
          brandObjWithIds[tire.brand] = brandObjWithIds[tire.brand].concat([
            tire.id,
          ]);
        } else {
          brandObjWithIds[tire.brand] = [tire.id];
        }
        if (!brandsObj[tire.brand])
          brandsObj[tire.brand] = { value: tire.brand, isChecked: false };
        if (!sizesObj[tire.size.toString()]) {
          sizesObj[tire.size.toString()] =
            sizeStateObject[tire.size.toString()];
        }
      });
      batch(() => {
        dispatch(setTires(tires));
        dispatch(setTiresByShopId(shopsObj));
        dispatch(setShops(shopsArr));
        dispatch(setShopsIndexed(shops));
        dispatch(addBrandsFilters(brandsObj));
        dispatch(addSizesFilters(sizesObj));
        dispatch(setTiresIndexed(tiresObj));
        dispatch(setTiresByType(typeObj));
        // dispatch(setTiresByShopId(shopsObj));
        dispatch(setTiresByBrand(brandObjWithIds));
        dispatch(setDataLoading(false));
      })
    })
    .catch((err) => console.error(err));
}

export default (state: ITireDenormalized[] = [], action: ITireAction) => {
  switch (action.type) {
    case SET_TIRES:
      return action.payload;
    default:
      return state;
  }
};