const SET_FILTERED_TIRES = "SET_FILTERED_TIRES";
import { ITireDenormalized } from '../';

interface ITireAction {
  type: string;
  payload: ITireDenormalized[];
}

export const setFilteredTires = (tires: ITireDenormalized[]): ITireAction => ({
  type: SET_FILTERED_TIRES,
  payload: tires,
});

export default (state: ITireDenormalized[] = [], action: ITireAction): ITireDenormalized[] => {
  switch (action.type) {
    case SET_FILTERED_TIRES:
      return action.payload;
    default:
      return state;
  }
};