const ADD_BRAND_FILTER = "ADD_BRAND_FILTER";
const ADD_BRANDS_FILTERS = "ADD_BRANDS_FILTERS";
const CHECK_BRAND_FILTER = "CHECK_BRAND_FILTER";

export interface IFilterBrandObject {
  value: string;
  isChecked: boolean;
}

export interface IFiltersBrands {
  [key: string]: IFilterBrandObject;
}

interface IFilterBrandAction {
  type: string;
  payload: string;
}

export const addBrandFilter = (brand: string) => ({
  type: ADD_BRAND_FILTER,
  payload: brand
});

export const addBrandsFilters = (brands: IFiltersBrands) => ({
  type: ADD_BRANDS_FILTERS,
  payload: brands
});

export const changeFilterCheck = (filterToChange: string) => ({
  type: CHECK_BRAND_FILTER,
  payload: filterToChange
});

export default (state: IFiltersBrands = {}, action: IFilterBrandAction) => {
  switch (action.type) {
    case ADD_BRAND_FILTER:
      let newState = { ...state };
      let key = action.payload;
      newState[key] = { value: key, isChecked: false };
      return newState;
    case ADD_BRANDS_FILTERS:
      return action.payload;
    case CHECK_BRAND_FILTER:
      let newState1 = { ...state };
      let filter = action.payload;
      newState1[filter].isChecked = !newState1[filter].isChecked
      return newState1;
    default:
      return state;
  }
};
