import React, { useEffect } from 'react';
// import { useParams } from 'react-router-dom';
import { connect } from 'react-redux';
import axios from 'axios';
import Map from '../components/Map';
import Header from '../components/Header';
import Footer from '../components/Footer';
import ListTiresMap from '../components/tiresMapView/ListTiresMap';
import ShopInfoWindow from '../components/tiresMapView/ShopInfoWindow';
import {
  IState,
  ICenter,
  IShop,
  ITireDenormalized,
  EnumLayouts,
  setFilteredShopsIDS,
} from '../../store';
import { getDistance } from '../../utils';
import { Modal } from 'react-responsive-modal';
import ShopPage from '../components/ShopPage';
import { Helmet } from 'react-helmet';
import cities from '../../utils/cities';

let renderCount = 0;

import {
  setCenter,
  setTires,
  setShops,
  setShopsIndexed,
  setTiresIndexed,
  setTiresByShopId,
  addBrandsFilters,
  setTiresByType,
  setTiresByBrand,
  changeIsModalOpen,
  // setSelectedShop,
  setDataLoading,
  addSizesFilters,
  sizeStateObject,
  fetchTires,
  setTiresBySize,
} from '../../store';
import tiresBySize from '../../store/reducers/tiresBySize';

interface IMapStateProps {
  lat: number;
  lng: number;
  cityName: string;
  center: ICenter;
  shops: IShop[];
  selectedShop: IShop[];
  isModalOpen: boolean;
  isDataLoading: boolean;
  selectedLayout: EnumLayouts;
}

interface IMapDispatchProps {
  setTiresDispatch: (tires: ITireDenormalized[]) => void;
  setShopsDispatch: (shopsArr: IShop[]) => void;
  setShopsIndexedDispatch: (shops: any) => void;
  addBrandsFiltersDispatch: (brandsObj: any) => void;
  addSizesFiltersDispatch: (sizesObj: any) => void;
  setTiresIndexedDispatch: (tiresObj: any) => void;
  setTiresByTypeDispatch: (typeObj: any) => void;
  setTiresByShopIdDispatch: (shopsObj: any) => void;
  setTiresByBrandDispatch: (brandObjWithIds: any) => void;
  // setSelectedShopDispatch: (selectedShop: any) => void;
  changeIsModalOpenDispatch: () => void;
  setDataLoadingDispatch: (condition: boolean) => void;
  setCenterDispatch: (center: any) => void;
  fetchTiresThunk: (lat: number, lng: number) => void;
  setFilteredShopsIDSDispatch: (ids: string[]) => void;
  setTiresBySizeDispatch: (sizesObjWithIds: any) => void;
}
interface ITireProps extends IMapStateProps, IMapDispatchProps {}

function Tires(props: ITireProps): JSX.Element {
  const {
    selectedShop,
    setTiresDispatch,
    setShopsDispatch,
    setShopsIndexedDispatch,
    addBrandsFiltersDispatch,
    setTiresIndexedDispatch,
    setTiresByTypeDispatch,
    setTiresByShopIdDispatch,
    setTiresByBrandDispatch,
    isModalOpen,
    changeIsModalOpenDispatch,
    setDataLoadingDispatch,
    isDataLoading,
    addSizesFiltersDispatch,
    setCenterDispatch,
    fetchTiresThunk,
    selectedLayout,
    resetStoreDispatch,
    setFilteredShopsIDSDispatch,
    setTiresBySizeDispatch
  } = props;

  // console.log('TIRES PAGE RENDER COUNT ', renderCount);

  let { lat, lng, cityName, center } = props;
  lat = Number(lat);
  lng = Number(lng);

  const fetchTiresApi = () => {
    setDataLoadingDispatch(true);
    if (lat === 0 && lng === 0) {
      const location = props.match.params.location;
      const newLat = location.split(',')[0];
      const newLNG = location.split(',')[1];
      const newCityName = cities[location];
      lat = newLat;
      lng = newLNG;
      cityName = newCityName;
      setCenterDispatch({ address: '', cityName, lat, lng });
    }
    // fetchTiresThunk(lat, lng);
    axios
      .get(`${API_ROOT}/${lat},${lng}`)
      .then((res) => res.data)
      .then((tires) => {
        if (tires.length) {
          setTiresDispatch(tires);
          // shopId -> shop
          const shops = {};
          // shopId -> [tiresIds]
          const shopsObj = {};
          // tireId -> tire;
          const tiresObj = {};
          const typeObj = {
            All: [],
            Trailer: [],
            Drive: [],
            Steer: [],
          };
          let shopsArr = [];
          const brandsObj = {};
          const sizesObj = {};
          const brandObjWithIds = {};
          const sizesObjWithIds = {};
          if (tires.length) {
            tires.forEach((tire) => {
              tiresObj[tire.id] = tire;
              if (!shops[tire.shopId]) {
                // const shop = {
                //   id: 0,
                //   name: '',
                //   shortName: '',
                //   address: '',
                //   lat: 0,
                //   lng: 0,
                //   distance: 0,
                // };
                const shop = {} as IShop;
                const shopLat = Number(JSON.parse(tire.geoJson).coordinates[1]);
                const shopLng = Number(JSON.parse(tire.geoJson).coordinates[0]);
                const distance = getDistance(
                  { lat, lng },
                  { lat: shopLat, lng: shopLng }
                );
                shop.id = tire.shopId;
                shop.name = tire.shopName;
                shop.shortName = tire.shopShortName;
                shop.address = tire.address;
                shop.phone = tire.phone;
                shop.phoneAfterHours = tire.phoneAfterHours;
                shop.lat = shopLat;
                shop.lng = shopLng;
                shop.roadService = tire.roadService;
                shop.inShopService = tire.inShopService;
                shop.open247 = tire.open247;
                shop.open247Mobile = tire.open247Mobile;
                shop.mounting = tire.mounting;
                shop.mountingAfterHours = tire.mountingAfterHours;
                shop.balancing = tire.balancing;
                shop.disposal = tire.disposal;
                shop.mobileFee = tire.mobileFee;
                shop.mobileFeeAfterHours = tire.mobileFeeAfterHours;
                shop.mobilePerHour = tire.mobilePerHour;
                shop.mobilePerHourAfterHours = tire.mobilePerHourAfterHours;
                shop.mobilePerMile = tire.mobilePerMile;
                shop.mobilePerMileAfterHours = tire.mobilePerMileAfterHours;
                shop.websiteName = tire.websiteName;
                shop.hours = tire.hours;
                shop.mobileHourMin = tire.mobileHourMin;
                shop.mobileHourMinAfterHours = tire.mobileHourMinAfterHours;
                shop.valveStem = tire.valveStem;
                shop.serviceInfo = tire.serviceInfo;
                shop.distance = distance;
                shops[tire.shopId] = shop;
                shopsArr = [...shopsArr, shop];
              }
              if (shopsObj[tire.shopId]) {
                shopsObj[tire.shopId] = shopsObj[tire.shopId].concat([tire.id]);
              } else {
                shopsObj[tire.shopId] = [tire.id];
              }
              if (typeObj[tire.type]) {
                typeObj[tire.type] = typeObj[tire.type].concat([tire.id]);
              } else {
                typeObj[tire.type] = [tire.id];
              }
              if (brandObjWithIds[tire.brand]) {
                brandObjWithIds[tire.brand] = brandObjWithIds[
                  tire.brand
                ].concat([tire.id]);
              } else {
                brandObjWithIds[tire.brand] = [tire.id];
              }
              if (!brandsObj[tire.brand])
                brandsObj[tire.brand] = { value: tire.brand, isChecked: false };
              if (!sizesObj[tire.size.toString()]) {
                sizesObj[tire.size.toString()] =
                  sizeStateObject[tire.size.toString()];
              }
              if (sizesObjWithIds[tire.size]) {
                sizesObjWithIds[tire.size] = sizesObjWithIds[
                  tire.size
                ].concat([tire.id]);
              } else {
                sizesObjWithIds[tire.size] = [tire.id];
              }
            });
          }

          setTiresByShopIdDispatch(shopsObj);
          setShopsDispatch(shopsArr);
          setShopsIndexedDispatch(shops);
          setFilteredShopsIDSDispatch(Object.keys(shops));
          addBrandsFiltersDispatch(brandsObj);
          addSizesFiltersDispatch(sizesObj);
          setTiresIndexedDispatch(tiresObj);
          setTiresByTypeDispatch(typeObj);
          // setTiresByShopIdDispatch(shopsObj);
          setTiresByBrandDispatch(brandObjWithIds);
          setTiresBySizeDispatch(sizesObjWithIds);
          // setDataLoadingDispatch(false);
        }
      })
      .then((data) => {
        setDataLoadingDispatch(false);
      })
      .catch((err) => console.error(err));
  };

  renderCount = renderCount + 1;

  const API_ROOT =
    'https://mebqwvo3g1.execute-api.us-east-1.amazonaws.com/default/getTires';

  useEffect(() => {
    if (process.env.BROWSER && !window.INITIAL_STATE.tires.length) {
      fetchTiresApi();
    }
  }, []);

  useEffect(() => {
    fetchTiresApi();
    try {
      // trying to use new API - https://developer.mozilla.org/en-US/docs/Web/API/Window/scrollTo
      window.scroll({
        top: 0,
        left: 0,
        behavior: 'smooth',
      });
    } catch (error) {
      // just a fallback for older browsers
      window.scrollTo(0, 0);
    }
  }, [lat, lng, cityName]);

  // useEffect(() => {
  //   if (process.env.BROWSER && !cityName) {
  //     axios
  //       .get(
  //         `https://maps.googleapis.com/maps/api/geocode/json?latlng=${lat},${lng}&key=${process.env.GOOGLE_API_KEY}`
  //       )
  //       .then((res) => res.data)
  //       .then((coord) => {
  //         const { formatted_address } = coord.results[0];
  //         const cityNameElements = formatted_address.split(', ');
  //         let cityName = '';
  //         if (cityNameElements.length > 1) {
  //           let len = cityNameElements.length;
  //           cityName =
  //             cityNameElements[len - 3] + ', ' + cityNameElements[len - 2];
  //         }
  //         cityName = cityName.split(' ').slice(0, -1).join(' ');
  //         const center = {
  //           address: formatted_address,
  //           lat,
  //           lng,
  //           cityName,
  //         };
  //         setCenterDispatch(center);
  //       });
  //   }
  // }, []);

  // isOpen={this.state.showModal}
  // contentLabel="onRequestClose Example"
  // onRequestClose={this.handleCloseModal}
  // shouldCloseOnOverlayClick={true}

  // let mapView = false;
  // if (selectedLayout === EnumLayouts.MAP) mapView = true;

  // let defaultCenter = {
  //   lat: Number(lat) - 0.15,
  //   lng: Number(lng) - 0.5,
  // };
  // if (mapView) defaultCenter = { lat: Number(lat), lng: Number(lng) };

  return (
    <React.Fragment>
      <Helmet>
        <title>Aidride | Find Commercial Truck Tires near {cityName}</title>
        <meta
          name='description'
          content='Commercial Truck Tires, Prices, Estimates, Road Service, All Position Tires, Steer Axle Tires, Drive Axle Tires, Trailer Tires'
        />
        <meta
          name='keywords'
          content='truck tires, quality tires, breakdowns, truck repair shops, tire dealers near me, truck stop near me, roadservice, steer tires, drive, trailer tires, tire easy'
        />
        <meta
          property='og:image'
          content='https://elasticbeanstalk-us-east-1-437366140317.s3.amazonaws.com/logo/58605632_padded_logo.png'
        />
        <meta
          property='og:title'
          content={`Aidride | Commercial Tires Prices near ${cityName}`}
        />
      </Helmet>
      <div className='tiresPage' data-testid='component-tires-page'>
        <Modal
          open={isModalOpen}
          onClose={() => changeIsModalOpenDispatch()}
          // classNames={{
          //   overlay: 'react__modal-overlay',
          //   modal: 'react__modal--content',
          // }}
          center
          blockScroll={false}
        >
          {selectedShop.length && <ShopPage />}
        </Modal>
        <Header />
        <div className='tiresPage__mapWrapper'>
          <ListTiresMap />
          {/* <ListShopsMap /> */}
          {/* <Map markers={shops} /> */}
          <Map
            center={{ address: '', lat, lng, cityName }}
            // defaultCenter={defaultCenter}
          />
          {selectedShop.length && <ShopInfoWindow />}
        </div>
      </div>
      <Footer />
    </React.Fragment>
  );
}

const mapStateToProps = (state: IState): IMapStateProps => {
  return {
    lat: state.center.lat,
    lng: state.center.lng,
    cityName: state.center.cityName,
    center: state.center,
    shops: state.shops,
    selectedShop: state.selectedShop,
    isModalOpen: state.isModalOpen,
    isDataLoading: state.isDataLoading,
    selectedLayout: state.selectedLayout,
  };
};

const mapDispatch = (dispatch): IMapDispatchProps => {
  return {
    setTiresDispatch(tires) {
      dispatch(setTires(tires));
    },
    setShopsDispatch(shopsArr) {
      dispatch(setShops(shopsArr));
    },
    setShopsIndexedDispatch(shops) {
      dispatch(setShopsIndexed(shops));
    },
    addBrandsFiltersDispatch(brandsObj) {
      dispatch(addBrandsFilters(brandsObj));
    },
    setTiresIndexedDispatch(tiresObj) {
      dispatch(setTiresIndexed(tiresObj));
    },
    setTiresByTypeDispatch(typeObj) {
      dispatch(setTiresByType(typeObj));
    },
    setTiresByShopIdDispatch(shopsObj) {
      dispatch(setTiresByShopId(shopsObj));
    },
    setTiresByBrandDispatch(brandObjWithIds) {
      dispatch(setTiresByBrand(brandObjWithIds));
    },
    // setSelectedShopDispatch(selectedShop) {
    //   dispatch(setSelectedShop(selectedShop));
    // },
    changeIsModalOpenDispatch() {
      dispatch(changeIsModalOpen());
    },
    setDataLoadingDispatch(condition) {
      dispatch(setDataLoading(condition));
    },
    addSizesFiltersDispatch(sizesObj) {
      dispatch(addSizesFilters(sizesObj));
    },
    setCenterDispatch(center) {
      dispatch(setCenter(center));
    },
    fetchTiresThunk(lat, lng) {
      dispatch(fetchTires(lat, lng));
    },
    setFilteredShopsIDSDispatch(shopsIds) {
      dispatch(setFilteredShopsIDS(shopsIds));
    },
    setTiresBySizeDispatch(tiresBySize) {
      dispatch(setTiresBySize(tiresBySize));
    },
    resetStoreDispatch() {
      dispatch({
        type: 'RESET_STORE',
      });
    },
  };
};

const Chicago: ICenter = {
  address: 'Chicago, IL',
  lat: 41.8781136,
  lng: -87.62979819999998,
};

const LosAngeles: ICenter = {
  address: 'Los Angeles, CA',
  lat: 34.0522342,
  lng: -118.2436849,
};

const loadData = (store, match) => {
  const lat = match.params.location.split(',')[0];
  const lng = match.params.location.split(',')[1];
  let cityName = '';
  if (cities[match.params.location]) cityName = cities[match.params.location];

  store.dispatch(setCenter({ address: '', lat, lng, cityName }));
  store.dispatch(setDataLoading(true));
  return axios
    .get(
      `https://mebqwvo3g1.execute-api.us-east-1.amazonaws.com/default/getTires/${match.params.location}`
    )
    .then((res) => res.data)
    .then((tires) => {
      // O(n) n -> tires length
      store.dispatch(setTires(tires));
      // shopId -> shop
      const shops = {};
      // shopId -> [tiresIds]
      const shopsObj = {};
      // tireId -> tire;
      const tiresObj = {};
      const typeObj = {
        All: [],
        Trailer: [],
        Drive: [],
        Steer: [],
      };
      let shopsArr = [];
      const brandsObj = {};
      const sizesObj = {};
      const brandObjWithIds = {};
      tires.forEach((tire) => {
        tiresObj[tire.id] = tire;
        if (!shops[tire.shopId]) {
          // const shop = {
          //   id: 0,
          //   name: '',
          //   shortName: '',
          //   address: '',
          //   lat: 0,
          //   lng: 0,
          //   distance: 0,
          // };
          const shop = {} as IShop;
          const shopLat = Number(JSON.parse(tire.geoJson).coordinates[1]);
          const shopLng = Number(JSON.parse(tire.geoJson).coordinates[0]);
          const distance = getDistance(
            { lat, lng },
            { lat: shopLat, lng: shopLng }
          );
          shop.id = tire.shopId;
          shop.name = tire.shopName;
          shop.shortName = tire.shopShortName;
          shop.address = tire.address;
          shop.phone = tire.phone;
          shop.phoneAfterHours = tire.phoneAfterHours;
          shop.lat = shopLat;
          shop.lng = shopLng;
          shop.roadService = tire.roadService;
          shop.inShopService = tire.inShopService;
          shop.open247 = tire.open247;
          shop.open247Mobile = tire.open247Mobile;
          shop.mounting = tire.mounting;
          shop.mountingAfterHours = tire.mountingAfterHours;
          shop.balancing = tire.balancing;
          shop.disposal = tire.disposal;
          shop.mobileFee = tire.mobileFee;
          shop.mobileFeeAfterHours = tire.mobileFeeAfterHours;
          shop.mobilePerHour = tire.mobilePerHour;
          shop.mobilePerHourAfterHours = tire.mobilePerHourAfterHours;
          shop.mobilePerMile = tire.mobilePerMile;
          shop.mobilePerMileAfterHours = tire.mobilePerMileAfterHours;
          shop.websiteName = tire.websiteName;
          shop.hours = tire.hours;
          shop.mobileHourMin = tire.mobileHourMin;
          shop.mobileHourMinAfterHours = tire.mobileHourMinAfterHours;
          shop.distance = distance;
          shops[tire.shopId] = shop;
          shopsArr = [...shopsArr, shop];
        }
        if (shopsObj[tire.shopId]) {
          shopsObj[tire.shopId] = shopsObj[tire.shopId].concat([tire.id]);
        } else {
          shopsObj[tire.shopId] = [tire.id];
        }
        if (typeObj[tire.type]) {
          typeObj[tire.type] = typeObj[tire.type].concat([tire.id]);
        } else {
          typeObj[tire.type] = [tire.id];
        }
        if (brandObjWithIds[tire.brand]) {
          brandObjWithIds[tire.brand] = brandObjWithIds[tire.brand].concat([
            tire.id,
          ]);
        } else {
          brandObjWithIds[tire.brand] = [tire.id];
        }
        if (!brandsObj[tire.brand])
          brandsObj[tire.brand] = { value: tire.brand, isChecked: false };
        if (!sizesObj[tire.size.toString()]) {
          sizesObj[tire.size.toString()] =
            sizeStateObject[tire.size.toString()];
        }
      });
      store.dispatch(setShops(shopsArr));
      store.dispatch(setShopsIndexed(shops));
      store.dispatch(addBrandsFilters(brandsObj));
      store.dispatch(addSizesFilters(sizesObj));
      store.dispatch(setTiresIndexed(tiresObj));
      store.dispatch(setTiresByType(typeObj));
      store.dispatch(setTiresByShopId(shopsObj));
      store.dispatch(setTiresByBrand(brandObjWithIds));
      store.dispatch(setDataLoading(false));
    })
    .catch((err) => console.error(err));
};

export default {
  loadData,
  component: connect(mapStateToProps, mapDispatch)(Tires),
};
