const SORT_PRICE = "SORT_PRICE";
const SORT_DISTANCE = "SORT_DISTANCE";

export const sortByDistance = () => ({
  type: SORT_DISTANCE,
  sortBy: 'distance'
});

export const sortByPrice = () => ({
  type: SORT_PRICE,
  sortBy: 'price'
});

// export const sortByDistance = () => (dispatch, getState) => {
