//   // sort tires by distance
//   // call action to set sorted tires as filtered tires
// };

// export const sortByPrice = () => (dispatch, getState) => {
//   const { tires } = getState();
//   // sort tires by price
//   // call action to set sorted tires as filtered tires
// };

export default (state = "price", action) => {
  switch (action.type) {
    case SORT_PRICE:
      return action.sortBy;
    case SORT_DISTANCE:
      return action.sortBy;
    default:
      return state;
  }
};
