const OPEN_247_MOBILE_SELECTED = 'OPEN_247_MOBILE_SELECTED';

interface IChangeOpen247MobileSelected {
  type: string;
}

export const changeOpen247Mobile = (): IChangeOpen247MobileSelected => ({
  type: OPEN_247_MOBILE_SELECTED
});

export default (state: boolean = false, action: IChangeOpen247MobileSelected) => {
  switch (action.type) {
    case OPEN_247_MOBILE_SELECTED:
      return !state;
    default:
      return state
  }
}