import React from 'react';
import cities from '../../../utils/cities';
import { Link } from 'react-router-dom';
import slugify from 'slugify';
import { connect } from 'react-redux';
import { IState, setCenter } from '../../../store';

function LocationsLinks(props) {
  const coordinates = Object.keys(cities);

  const { setCenterDispatch, resetStoreDispatch } = props;

  const changeLocation = (coord) => {
    resetStoreDispatch();
    setCenterDispatch({
      address: '',
      lat: Number(coord.split(',')[0]),
      lng: Number(coord.split(',')[1]),
      cityName: cities[coord],
    });
  };

  return (
    <div className='footer__locations'>
      <h3 className='footer__locations-h3'>Quick Search Links&nbsp;</h3>
      {coordinates.map((coord) => (
        <Link
          className='home__quickSearch-link'
          to={`/commercial-tires-near/${slugify(cities[coord])}/${coord}`}
          onClick={() => changeLocation(coord)}
        >
          {cities[coord]}&nbsp;
        </Link>
      ))}
    </div>
  );
}

const mapState = (state: IState) => {
  return {};
};

const mapDispatch = (dispatch) => {
  return {
    setCenterDispatch(center) {
      dispatch(setCenter(center));
    },
    resetStoreDispatch() {
      dispatch({
        type: 'RESET_STORE',
      });
    },
  };
};

export default connect(mapState, mapDispatch)(LocationsLinks);
