const mapStateToProps = (state: IState): IMapStateProps => {
  return {
    center: state.center,
    shops: state.shops,
    selectedShop: state.selectedShop,
    tiresHomePage: state.tiresHomePage,
  };
};

const mapDispatch = (dispatch): IMapDispatchProps => {
  return {
    setCenterDispatch(center) {
      dispatch(setCenter(center));
    },
    resetStoreDispatch() {
      dispatch({
        type: 'RESET_STORE',
      });
    },
  };
};

