import React from 'react';
import { connect } from 'react-redux';
import {
  IState,
  EnumDropdowns,
  setSelectedDropdown,
  changeFilterSize,
} from '../../../store';

function FiltersSize(props) {
  const {
    selectedDropdown,
    setSelectedDropdownDispatch,
    filtersSize,
    changeFilterSizeDispatch,
  } = props;
  let btnCSS = 'filters-sizeDropdown dropdown__btn';
  let dropCSS = 'invis';
  if (selectedDropdown === EnumDropdowns.SIZE) {
    btnCSS = 'filters-sizeDropdown dropdown__btn dropdown__btn-active';
    dropCSS = 'dropdown__content';
  }
  const handleClick = () => {
    if (selectedDropdown === EnumDropdowns.SIZE) {
      setSelectedDropdownDispatch(EnumDropdowns.NONE);
    } else {
      setSelectedDropdownDispatch(EnumDropdowns.SIZE);
    }
  };
  return (
    <div className='filters-sizeDropdown'>
      <button className={btnCSS} onClick={() => handleClick()}>
        Size&nbsp;
      </button>
      <div data-testid='filters-size-dropdown' className={dropCSS}>
        {Object.keys(filtersSize).map((filterKey, id) => {
          return (
            <label className='checkbox__label' key={id}>
              <input
                type='checkbox'
                className='checkbox__input'
                value={filtersSize[filterKey].value}
                checked={filtersSize[filterKey].isChecked}
                onChange={() => changeFilterSizeDispatch(filterKey)}
                data-testid='size-filter-btn'
              />
              <span className='checkbox__custom'></span>
              {filtersSize[filterKey].label}&nbsp;
            </label>
          );
        })}
      </div>
    </div>
  );
}

const mapState = (state: IState) => {
  return {
    selectedDropdown: state.selectedDropdown,
    filtersSize: state.filtersSize,
  };
};

const mapDispatch = (dispatch) => {
  return {
    setSelectedDropdownDispatch(sd: string) {
      dispatch(setSelectedDropdown(sd));
    },
    changeFilterSizeDispatch(value: string | number) {
      dispatch(changeFilterSize(value));
    },
  };
};

export default connect(mapState, mapDispatch)(FiltersSize);
