/* eslint-disable react/self-closing-comp */
/* eslint-disable jsx-a11y/label-has-associated-control */
import React from 'react';
import { connect } from 'react-redux';
import {
  changeAllSelected,
  changeSteerSelected,
  changeDriveSelected,
  changeTrailerSelected,
  IState,
  IFilterObject,
  IFiltersType,
} from '../../../store';

interface IFiltersTypeProps extends IFiltersType {
  allIsChecked: boolean;
  steerIsChecked: boolean;
  driveIsChecked: boolean;
  trailerIsChecked: boolean;
  changeAllSelectedDispatch: any;
  changeSteerSelectedDispatch: any;
  changeDriveSelectedDispatch: any;
  changeTrailerSelectedDispatch: any;
}

function FiltersType(props: IFiltersTypeProps) {
  const {
    all,
    allIsChecked,
    steer,
    steerIsChecked,
    drive,
    driveIsChecked,
    trailer,
    trailerIsChecked,
    changeAllSelectedDispatch,
    changeSteerSelectedDispatch,
    changeDriveSelectedDispatch,
    changeTrailerSelectedDispatch,
  } = props;

  return (
    <div className='filters-type'>
      <label className='checkbox__label'>
        <input
          type='checkbox'
          className='checkbox__input'
          // value={'All'}
          value={all.value}
          checked={allIsChecked}
          onChange={() => changeAllSelectedDispatch()}
          data-testid='all-type-filter-btn'
        />
        <span className='checkbox__custom'></span>
        All&nbsp;
      </label>
      <label className='checkbox__label'>
        <input
          type='checkbox'
          className='checkbox__input'
          // value={'Steer'}
          value={steer.value}
          checked={steerIsChecked}
          onChange={() => changeSteerSelectedDispatch()}
          data-testid='steer-type-filter-btn'
        />
        <span className='checkbox__custom'></span>
        Steer&nbsp;
      </label>
      <label className='checkbox__label'>
        <input
          type='checkbox'
          className='checkbox__input'
          // value={'Drive'}
          value={drive.value}
          checked={driveIsChecked}
          onChange={() => changeDriveSelectedDispatch()}
          data-testid='drive-type-filter-btn'
        />
        <span className='checkbox__custom'></span>
        Drive&nbsp;
      </label>
      <label className='checkbox__label'>
        <input
          type='checkbox'
          className='checkbox__input'
          // value={'Trailer'}
          value={trailer.value}
          checked={trailerIsChecked}
          onChange={() => changeTrailerSelectedDispatch()}
          data-testid='trailer-type-filter-btn'
        />
        <span className='checkbox__custom'></span>
        Trailer&nbsp;
      </label>
    </div>
  );
}

const mapState = (state: IState) => {
  return {
    all: state.filtersType.all,
    allIsChecked: state.filtersType.all.isChecked,
    steer: state.filtersType.steer,
    steerIsChecked: state.filtersType.steer.isChecked,
    drive: state.filtersType.drive,
    driveIsChecked: state.filtersType.drive.isChecked,
    trailer: state.filtersType.trailer,
    trailerIsChecked: state.filtersType.trailer.isChecked,
  };
};

const mapDispatch = (dispatch) => {
  return {
    changeAllSelectedDispatch() {
      dispatch(changeAllSelected());
    },
    changeSteerSelectedDispatch() {
      dispatch(changeSteerSelected());
    },
    changeDriveSelectedDispatch() {
      dispatch(changeDriveSelected());
    },
    changeTrailerSelectedDispatch() {
      dispatch(changeTrailerSelected());
    },
  };
};

export default connect(mapState, mapDispatch)(FiltersType);
