const CHANGE_IS_MODAL_OPEN = 'CHANGE_IS_MODAL_OPEN';

interface IChangeIsModalOpen {
  type: string;
}

export const changeIsModalOpen = (): IChangeIsModalOpen => ({
  type: CHANGE_IS_MODAL_OPEN
});

export default (state: boolean = false, action: IChangeIsModalOpen) => {
  switch (action.type) {
    case CHANGE_IS_MODAL_OPEN:
      return !state;
    default:
      return state
  }
}