import React from 'react';
import { connect } from 'react-redux';
import { IState, ITire, ITireDenormalized } from '../../../store';
import SVGTire from '../svg/SVGTire';

interface ITireCardTiresMapState {
  tiresHomePage: ITire[];
}

interface ITireCardTiresMapDispatch {}

interface ITireCardTireHomePageProps
  extends ITireDenormalized,
    ITireCardTiresMapState,
    ITireCardTiresMapDispatch {
  idx?: any;
  idxSeqLightBlue?: string[];
  idxSeqBlue?: string[];
  idxSeqGreen?: string[];
  idxSeqRed?: string[];
}

function TireCardTiresHomePage(props: ITireCardTireHomePageProps): JSX.Element {
  let { beforeDiscountPrice } = props;
  const {
    id,
    shopId,
    distance,
    brand,
    model,
    netPrice,
    finalOutTheDoorPrice,
    mounting,
    disposal,
    imgLink,
    type,
    inShopService
  } = props;

  let { shopShortName } = props;

  let tagClass = '';
  let theme = 'dark';
  if (type === 'Drive')
    tagClass = `tct-${theme}-tags-tag tct-${theme}-tags-tag-drive`;
  if (type === 'Steer')
    tagClass = `tct-${theme}-tags-tag tct-${theme}-tags-tag-steer`;
  if (type === 'Trailer')
    tagClass = `tct-${theme}-tags-tag tct-${theme}-tags-tag-trailer`;
  if (type === 'All')
    tagClass = `tct-${theme}-tags-tag tct-${theme}-tags-tag-all`;
  let CLASS = 'tires-card-tires-tags-tag ' + tagClass;

  const handleClick = (id) => {
    console.log(id);
  };

  if (shopShortName && shopShortName.length > 13) {
    shopShortName = shopShortName.slice(0, 13);
  }

  if (netPrice > 0) {
    let randomPercent: number[] = [0.12, 0.15, 0.17, 0.2, 0.25, 0.28];
    if (Number(beforeDiscountPrice) === 0) {
      let ranIdx = Math.floor(Math.random() * (randomPercent.length - 1)) + 1;
      beforeDiscountPrice = netPrice + netPrice * randomPercent[ranIdx];
      beforeDiscountPrice = parseFloat(beforeDiscountPrice).toFixed(2);
    }
  }

  let PRICE_CALC;
  if (netPrice && netPrice > 0 && !finalOutTheDoorPrice) {
    PRICE_CALC = netPrice;
    if (mounting && mounting > 0) {
      PRICE_CALC = PRICE_CALC + mounting;
    }
    if (disposal && disposal > 0) {
      PRICE_CALC = PRICE_CALC + disposal;
    }
    PRICE_CALC = parseFloat(PRICE_CALC).toFixed(2);
  } else {
    PRICE_CALC = finalOutTheDoorPrice;
  }

  return (
    <div
      className={`tires-card-tires tct-${theme} home-tire-card`}
      data-testid='tires-card-component'
      onClick={() => handleClick(id)}
    >
      <div className='tires-card-tires-shopName-wrap'>
        <h3 className={`tires-card-tires-shopName tct-${theme}-shopName`}>
          {shopShortName}&nbsp;
        </h3>
      </div>
      <div className={`tires-card-tires-tags tct-${theme}-tags`}>
        <div className={CLASS}>{type}&nbsp;</div>
        <div className={`tires-card-tires-tags-dist tct-${theme}-tags-dist`}>
          {distance} mi
        </div>
      </div>
      {/* {imgLink !== '' ? (
        <img className='tires-card-tires-img' src={imgLink}></img>
      ) : (
        <SVGNoImage />
      )} */}
      {/* <img
        className='tires-card-tires-img'
        src={`/tires/${type}-axle-position-commercial-truck-tire.png`}
        alt={
          model
            ? `${type} Position ${brand} ${model} Tire`
            : `${type} Position ${brand} Tire`
        }
      ></img> */}
      <SVGTire tireType={type} />
      <div className={`tires-card-tires-title tct-${theme}-title`}>
        <h2 className={`tires-card-tires-title-brand tct-${theme}-title-brand`}>
          {brand}&nbsp;
        </h2>
        <h2 className={`tires-card-tires-title-model tct-${theme}-title-model`}>
          {model ? model : 'Model Unspecified'}&nbsp;
        </h2>
      </div>
      <p className={`tires-card-tires-price tct-${theme}-price`}>
        <span className={`tires-card-tires-price-old tct-${theme}-price-old`}>
          {netPrice !== 0 ? `$${beforeDiscountPrice}` : `Final`}&nbsp;
        </span>{' '}
        ${netPrice !== 0 ? netPrice : PRICE_CALC}&nbsp;
      </p>
      <p className={`tires-card-tires-price-final tct-${theme}-price-final`}>
        {inShopService && !finalOutTheDoorPrice && `Shop Est. `}
        {inShopService && finalOutTheDoorPrice !== 0 && `Final Est. `}
        {inShopService && (
          <span
            className={`tires-card-tires-price-final-num tct-${theme}-price-final-num`}
          >
            {`$${PRICE_CALC}`}&nbsp;
          </span>
        )}
      </p>
    </div>
  );
}

const mapState = (state: IState): ITireCardTiresMapState => {
  return {
    tiresHomePage: state.tiresHomePage,
  };
};

const mapDispatch = (dispatch): ITireCardTiresMapDispatch => {
  return {};
};

export default connect(mapState, mapDispatch)(TireCardTiresHomePage);
