const SET_SELECTED_TIRE = "SET_SELECTED_TIRE";
import { IState, ITireDenormalized } from '../';

interface ISelectedTireAction {
  type: string;
  payload: ITireDenormalized;
}

export const setSelectedTire = (tire: ITireDenormalized): ISelectedTireAction => ({
  type: SET_SELECTED_TIRE,
  payload: tire
});

export const setSelectedTireById = (tireId: number) => (dispatch, getState: () => IState) => {
  const state = getState();
  // let tire = state.tires.filter(t=> t.id === tireId)[0];
  let tire = state.tiresIndexed[tireId]
  dispatch(setSelectedTire(tire));
}

export default (state: ITireDenormalized[] = [], action: ISelectedTireAction) => {
  switch (action.type) {
    case SET_SELECTED_TIRE:
      return [action.payload];
    default:
      return state;
  }
};