import { ITire, ITireDenormalized } from './tires';

const SET_TIRES_INDEXED = "SET_TIRES_INDEXED";

export interface ITiresIndexed {
  [key: string]: ITireDenormalized;
}

enum TiresIndexedActionTypes {
  setTiresIndexed
}

interface ISetTiresIndexedAction {
  type: string;
  payload: ITiresIndexed
}

export const setTiresIndexed = (tiresIndexed: ITiresIndexed): ISetTiresIndexedAction => ({
  type: SET_TIRES_INDEXED,
  payload: tiresIndexed
})

export default (state: ITiresIndexed = {}, action: ISetTiresIndexedAction) => {
  switch (action.type) {
    case SET_TIRES_INDEXED:
      return action.payload;
    default:
      return state;
  }
};