const OPEN_247_SELECTED = 'OPEN_247_SELECTED';

interface IChangeOpen247Selected {
  type: string;
}

export const changeOpen247 = (): IChangeOpen247Selected => ({
  type: OPEN_247_SELECTED
});

export default (state: boolean = false, action: IChangeOpen247Selected) => {
  switch (action.type) {
    case OPEN_247_SELECTED:
      return !state;
    default:
      return state
  }
}